import { WorkflowStep } from './../constants/workflow-step.enum';
import { Injectable } from '@angular/core';
import { AccountService } from 'src/app/authentication/services/account.service';
import { Roles } from 'src/app/core/constants/role.constants';
import { RegionSelectorService } from 'src/app/core/services/region-selector.service';
import { StringUtils } from 'src/app/shared/utilities/string-utils';

@Injectable({
  providedIn: 'root'
})
export class Carrier411RolesService {
  constructor(
    private accountService: AccountService,
    private regionSelectorService: RegionSelectorService
  ) {
  }

  public isAdmin = (): boolean => this.isEditor() || this.isReviewer() || this.isPublisher();

  public isEditor(): boolean {
    const role = StringUtils.format(Roles.Carrier411.Editor, '.', this.getCurrentRegion());
    return this.accountService.userHasRole(role);
  }

  public isReviewer(): boolean {
    const role = StringUtils.format(Roles.Carrier411.Reviewer, '.', this.getCurrentRegion());
    return this.accountService.userHasRole(role);
  }

  public isPublisher(): boolean {
    const role = StringUtils.format(Roles.Carrier411.Publisher, '.', this.getCurrentRegion());
    return this.accountService.userHasRole(role);
  }

  public getAllowedWorkflowSteps(): WorkflowStep[] {
    var workflowStep: WorkflowStep[] = [];
    if (this.isEditor()) return workflowStep;

    workflowStep.push(WorkflowStep.DeleteRequested);
    workflowStep.push(WorkflowStep.PendingApproval);

    if (this.isPublisher()) {
      workflowStep.push(WorkflowStep.PendingDelete);
      workflowStep.push(WorkflowStep.PendingPublish);
    }

    return workflowStep;
  }

  private getCurrentRegion = (): string | undefined => this.regionSelectorService.getCurrentRegion()?.abbreviation;
}
