import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { RegionDTO } from 'src/app/admin/region-management/interfaces/region.dto';

const REGION_KEY: string = 'CurrentRegion';

@Injectable({
  providedIn: 'root'
})
export class RegionSelectorService {
  // Observable region source
  public regionSource = new BehaviorSubject<RegionDTO | null>(null);
  // Observable region stream
  region$ = this.regionSource.asObservable();
  // service command
  changeRegion(region: RegionDTO) {
    if (region != null) {
      this.regionSource.next(region);
      window.sessionStorage.setItem(REGION_KEY, JSON.stringify(region));
    }
  }

  constructor() { }

  public getCurrentAbbreviation(): string | null | undefined {
    return this.getCurrentRegion()?.abbreviation;
  }

  public getCurrentRegion(): RegionDTO | null {
    return this.regionSource.getValue() ?? JSON.parse(window.sessionStorage.getItem(REGION_KEY)!);
  }
}
