import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable, Inject } from '@angular/core';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { RegionUpdateDTO } from '../interfaces/region.update.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { AdminApiEndpoints } from '../../constants/admin-api.constants';
import { RegionDTO } from '../interfaces/region.dto';

@Injectable({
  providedIn: "root"
})
export class RegionApiService extends BaseAPIService {
  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string,
    http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  public getWithFilter(countryId?: number, active?: boolean) {
    let params = new HttpParams()
      .set('countryId', countryId ?? '')
      .set('active', active ?? '');

    return this.http.get<ServiceResultDTO<RegionDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetRegionsWithFilter()), { params }
    );
  }

  public update(regions: RegionUpdateDTO[]) {
    return this.http.patch<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateRegions()), regions
    );
  }

  public validateUpdate(regions: RegionUpdateDTO[]) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateUpdateRegions()), regions
    );
  }
}
