import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import { Observable } from 'rxjs';
import { RegionSelectorService } from '../services/region-selector.service';

@Injectable()
export class CurrentRegionInterceptor implements HttpInterceptor {

  constructor(private regionSelectorService: RegionSelectorService) { }

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    const currentRegion = this.regionSelectorService.getCurrentRegion();
    if (currentRegion != null) {
      request = request.clone({
        setHeaders: {
          RegionId: `${currentRegion.id}`
        }
      })
    }

    return next.handle(request);
  }
}
