import * as crypto from "crypto-js";
import jwt_decode from "jwt-decode";
import { AuthTokenDTO } from "../interfaces/auth-token/auth-token.dto";
import { CryptoToolsDTO } from "../interfaces/crypto/crypto-tools.dto";
import { environment } from '../../../environments/environment';


export const cryptoTools: CryptoToolsDTO = (function (crypto, jwt_decode) {
  const sk = environment.cryptoKey;
  const cryptoTools: CryptoToolsDTO = {} as CryptoToolsDTO;

  cryptoTools.encryptObject = function (object: any): string | void {
    if (typeof object === "undefined"
      || typeof object !== "object") {
      return console.error("object was not passed");
    }

    const stringify = JSON.stringify(object);
    return (crypto.AES.encrypt(stringify, sk)).toString();
  }

  cryptoTools.decrypt = function (cipherText: string): any {
    if (typeof cipherText == "undefined"
      || typeof cipherText !== "string"
      || !cipherText) {
      return console.error("Cipher text was not passed");
    }

    const bytes = crypto.AES.decrypt(cipherText, sk);
    const decryptedString = bytes.toString(crypto.enc.Utf8);

    return JSON.parse(decryptedString);
  }

  cryptoTools.decodeJwt = function (token: string): AuthTokenDTO {
    const decoded = jwt_decode(token);
    return decoded as AuthTokenDTO;
  }

  return cryptoTools;
})(crypto, jwt_decode);
