import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { BaseAPIService } from "../../core/api/base.api.service";
import { ServiceResultDTO } from "../../core/interfaces/service-result/service-result.dto";
import { AuthenticationApiEndpoints } from "../constants/authentication-api.constants";
import { GaitApiEndpoints } from "../constants/gait-api.constants";
import { GaitUserDTO } from "../interfaces/gait/gait-user.dto";
import { WPUserAccountDTO } from "../interfaces/user/user-account.dto";


@Injectable({
  providedIn: "root"
})
export class GaitApiService extends BaseAPIService {
  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string,
    http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  public authenticateGaitUser(gaitUserDTO: GaitUserDTO) {
    return this.http.post<ServiceResultDTO<WPUserAccountDTO>>(
      this.constructRequestUrl(AuthenticationApiEndpoints.AuthenticateGaitUser()), gaitUserDTO
    );
  }

  public refrechContext(contextId: string) {
    return this.http.get<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(GaitApiEndpoints.RefreshContext(contextId)), 
    );
  }

  public checkContext(contextId: string) {
    return this.http.get<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(GaitApiEndpoints.CheckContext(contextId))
    );
  }

  public deleteContext(contextId: string) {
    return this.http.get<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(GaitApiEndpoints.DeleteContext(contextId))
    );
  }
}
