import { Component, OnInit } from '@angular/core';
import { DefaultButtonComponent } from '../default-button/default-button.component';
import { faTrashCan } from '@fortawesome/pro-regular-svg-icons';

@Component({
  selector: 'delete-button',
  templateUrl: '../default-button/default-button.component.html'
})
export class DeleteButtonComponent extends DefaultButtonComponent implements OnInit {
  constructor() {
    super();

    this.buttonIcon = faTrashCan;
    if (this.text == '') this.text = 'Delete';
  }

  ngOnInit(): void {
  }
}
