import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { ToolService } from 'src/app/admin/tools-management/services/tool.service';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';

@Component({
  selector: 'favorite-message-edit',
  templateUrl: './favorite-message-edit.component.html',
  styleUrls: ['./favorite-message-edit.component.scss']
})
export class FavoriteMessageEditComponent implements OnInit, OnChanges {
  @Input("description")
  public description: string = "";
  @Input("toolId")
  public toolId: number = 0;
  @Input('favoriteMessage')
  public favoriteMessage: string | undefined = "";
  public newMessage: string = "";

  constructor(
    private toolService: ToolService,
    private resultHandlerService: ResultHandlerService
  ) { }

  ngOnInit(): void {
  }

  ngOnChanges(): void {
    this.newMessage = this.favoriteMessage!;
  }

  onUndo() {
    this.newMessage = this.favoriteMessage!;
  }

  onSave() {
    this.toolService.updateFavoriteMessage(this.toolId, this.newMessage).subscribe((data) => {
      this.resultHandlerService.handle(data).then((success) => {
        if (success) this.favoriteMessage = data.value;
      });
    });
  }
}
