import { ApplicationUrls } from "../constants/application-urls";
import { ToolsList } from "../constants/tools-list.enum";
import { LinkDTO } from "../interfaces/link.dto";
import { StringUtils } from "./string-utils";

export abstract class UrlUtils {
  public static getToolUrl(toolId: number, currentPath?: string): string | null | undefined {
    if (ToolsList[toolId] != null) {
      switch (ToolsList[toolId]) {
        case ToolsList[ToolsList.Carrier411]:
          return ApplicationUrls.Carrier411.path;
        case ToolsList[ToolsList.LargeGroupRFP]:
          return ApplicationUrls.LargeGroupRFP.path;
        case ToolsList[ToolsList.DocumentToolkit]:
          return ApplicationUrls.DocumentToolkit.path;
        case ToolsList[ToolsList.SecureDirectUpload]:
          return ApplicationUrls.SecureDirectUpload.path;
        case ToolsList[ToolsList.TeamSheets]:
          return ApplicationUrls.TeamSheets.path;
        default:
          return '';
      }
    }

    return currentPath;
  }

  public static getLinksFromObject(p: any): LinkDTO[] {
    const links: LinkDTO[] = [];
    for (var key of Object.keys(p)) {
      if (typeof p[key] === 'object') {
        const link = this.getLinkFromObject(p[key]);
        if (link != null) links.push(link);
      }
    }

    return links;
  }

  public static getLinkFromObject(p: any): LinkDTO | null {
    if (p.path != undefined && p.path != '') {
      return {
        path: p.path,
        text: p.text
      };
    }

    return null;
  }

  public static replaceParamFromUrl(currentUrl: string, urlWithPlaceHolders: string): string {
    const partsUrl = currentUrl.split('/');
    const partsLink = urlWithPlaceHolders.split('/');

    const pathsWithParam = partsLink.filter(pl => pl.includes('{'));
    pathsWithParam.forEach(pl => {
      const index = partsLink.indexOf(pl);
      if (partsUrl.length >= index + 1 && !isNaN(+partsUrl[index])) {
        partsLink[index] = partsUrl[index];
      }
    });

    return partsLink.join('/');
  }

  public static replaceParams(urlWithPlaceHolders: string, ...params: any[]) {
    var url = urlWithPlaceHolders;
    params.forEach(p => {
      url = StringUtils.format(url, '/', p);
    });

    return url;
  }

  public static getPrettyFileName(filePath: string) {
    const fileNameWithoutPath = filePath.match(/[^\\/:*?"<>|\r\n]+$/gs);
    if (fileNameWithoutPath) {
      const startExtensionIndex = fileNameWithoutPath[0].lastIndexOf('.');
      const fileName = fileNameWithoutPath[0].substring(0, startExtensionIndex);
      const fileExtension = fileNameWithoutPath[0].substring(startExtensionIndex);

      return `${fileName.substring(0, fileName.length - 5)}${fileExtension}`;
    }

    return filePath;
  }
}
