import { NgModule } from '@angular/core';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { GaitInterceptor } from './interceptors/gait.interceptor';


@NgModule({
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: GaitInterceptor, multi: true, }
  ]
})
export class GaitInterceptorModule { }
