import { Component, OnInit } from '@angular/core';
import { WPUserAccountDTO } from 'src/app/authentication/interfaces/user/user-account.dto';
import { AccountService } from 'src/app/authentication/services/account.service';
import { faUser } from '@fortawesome/pro-light-svg-icons';
import { ItemModel, MenuEventArgs } from '@syncfusion/ej2-angular-splitbuttons';
import { UserType } from 'src/app/shared/constants/user-type.enum';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';

@Component({
  selector: 'user-settings',
  templateUrl: './user-settings.component.html',
  styleUrls: ['./user-settings.component.scss']
})
export class UserSettingsComponent implements OnInit {
  logOutText = 'Sign Out';
  faUser = faUser;
  user: WPUserAccountDTO | null = null;

  public items!: ItemModel[];

  constructor(
    private accountService: AccountService
  ) { }

  ngOnInit(): void {
    this.user = this.accountService.getCurrentUser();
    this.setupUserLinks();
  }

  setupUserLinks() {
    if (this.user?.userType == UserType.Broker) {
      this.items = UrlUtils.getLinksFromObject(ApplicationUrls.BrokerUserLinks).map(l => {
        return {
          text: l.text,
          url: l.path
        }
      })
      this.items.push(
        {
          separator: true
        },
        {
          text: this.logOutText
        }
      );      
    } else {
      this.items = [{
        text: this.logOutText
      }];
    }
  }

  public select(args: MenuEventArgs) {
    if (args.item.text === this.logOutText) this.signOut();
  }

  signOut() {
    this.accountService.logout();
  }
}
