import { WorkflowStep } from "src/app/carrier411/shared/constants/workflow-step.enum";
import { CommonDTO } from "../interfaces/common.dto";

export abstract class WorkflowStepUtils {
  public static getDescription(step: WorkflowStep): string | null | undefined {
    const list = WorkflowStepUtils.getList();
    const id = typeof step == 'number' ? step : +WorkflowStep[step];
    return list.filter(s => s.id == id)[0]?.name;
  }

  public static getList(): CommonDTO[] {
    return [
      { id: +WorkflowStep.Draft, name: 'Draft' },
      { id: +WorkflowStep.PendingApproval, name: 'Pending Approval' },
      { id: +WorkflowStep.PendingPublish, name: 'Pending Publish' },
      { id: +WorkflowStep.Published, name: 'Published' },
      { id: +WorkflowStep.DeleteRequested, name: 'Delete Requested' },
      { id: +WorkflowStep.PendingDelete, name: 'Pending Delete' },
      { id: +WorkflowStep.Deleted, name: 'Deleted' }
    ];
  }

  public static compareEnumValue(value: WorkflowStep, valueToCompare: WorkflowStep): boolean {
    const idValue = typeof value == 'number' ? value : +WorkflowStep[value];
    const idValueToCompare = typeof valueToCompare == 'number' ? valueToCompare : +WorkflowStep[valueToCompare];

    return idValue.toString() == idValueToCompare.toString();
  }
}
