import { NgModule, CUSTOM_ELEMENTS_SCHEMA, APP_INITIALIZER } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";

// Import library module
import { NgxSpinnerModule } from "ngx-spinner";
import { LoadingInterceptorModule } from './shared/loading.module';

// Components
import { AppComponent } from './app.component';
import { DashboardComponent } from './dashboard/components/dashboard.component';
import { ResourceCenterComponent } from './resource-center/components/resource-center-list/resource-center.component';
import { ResourceLinkListComponent } from './resource-center/components/resource-link-list/resource-link-list.component';

// Modules
import { AppRoutingModule } from './app-routing.module';
import { Router, RouterModule } from '@angular/router';
import { SharedModule } from './shared/shared.module';
import { SyncfusionModule } from './shared/syncfusion.module';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { JwtInterceptorModule } from './authentication/jwt.module';
import { GaitService } from './authentication/services/gait.service';
import { CoreModule } from './core/core.module';

//Services
import { AppStartupService, appStartupServiceFactory } from './core/services/app.startup.service';
import { AccountService } from './authentication/services/account.service';
import { ThemeService } from './core/services/theme.service';
import { GaitInterceptorModule } from './authentication/gait.module';

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    ResourceCenterComponent,
    ResourceLinkListComponent
   ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    RouterModule.forRoot([]),
    HttpClientModule,
    BrowserAnimationsModule,
    NgxSpinnerModule,
    LoadingInterceptorModule,
    SharedModule,
    SyncfusionModule,
    FontAwesomeModule,
    JwtInterceptorModule,
    GaitInterceptorModule,
    CoreModule
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  providers: [
    AccountService,
    GaitService,
    AppStartupService,
    {
      provide: APP_INITIALIZER,
      useFactory: appStartupServiceFactory,
      deps: [AppStartupService, Router],
      multi: true
    },
    ThemeService
  ],
  bootstrap: [
    AppComponent
  ]
})
export class AppModule { }
