let sharedApiEndpoints: SharedApiEndPointsType = {
  /* Tools */
  GetTool: (id: number) => `/tools/${id}`,
  GetTools: () => `/tools`,
  GetToolIsContentHidden: (id: number) => `/tools/${id}/content-hidden`,
  /* ResourceCenter */
  GetResourceCenter: () => `/resourcecenter`,
  /* User Settings */
  GetUserSettings: (userId: number) => `/users-settings/${userId}`,
  GetUserRegionSettings: (userId: number) => `/users-settings/${userId}/region`,
  UpdateUserMenuThemeSettings: (userId: number) => `/users-settings/${userId}/menu-theme`,
  UpdateUserRegionSettings: (userId: number) => `/users-settings/${userId}/region`
};

interface SharedApiEndPointsType {
  /* Tools */
  readonly GetTool: Function;
  readonly GetTools: Function;
  readonly GetToolIsContentHidden: Function;
  /* Resource Center */
  readonly GetResourceCenter: Function;
  /* User Settings */
  readonly GetUserSettings: Function;
  readonly GetUserRegionSettings: Function;
  readonly UpdateUserMenuThemeSettings: Function;
  readonly UpdateUserRegionSettings: Function;
}

export const SharedApiEndpoints: SharedApiEndPointsType = sharedApiEndpoints;
