import { AfterViewChecked, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { faScrewdriverWrench, faStar as faStarSolid } from '@fortawesome/pro-solid-svg-icons';
import { faHouse, faStar as faStarLight } from '@fortawesome/pro-light-svg-icons';
import { MenuLinkDTO } from '../../interfaces/menu-link.dto';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { MainMenuService } from '../../services/main-menu.service';
import { Observable } from 'rxjs';
import { getFilePathEncoded } from 'src/app/shared/utilities/shared';

@Component({
  selector: 'main-menu-links',
  templateUrl: './main-menu-links.component.html',
  styleUrls: ['./main-menu-links.component.scss']
})
export class MainMenuLinksComponent implements OnInit, AfterViewChecked {
  getFilePathEncoded = getFilePathEncoded;

  faScrewdriverWrench = faScrewdriverWrench;
  faStarLight = faStarLight;
  faStarSolid = faStarSolid;

  @Input()
  public title: string = '';
  @Input()
  public links: MenuLinkDTO[] = [];
  @Input()
  public isVisible: boolean = false;
  @Input()
  public hasDivider: boolean = false;
  @Input()
  public favorites!: MenuLinkDTO[];
  @Input()
  public sortable: boolean = false;
  @Input()
  public showDashboard: boolean = false;

  public draggingLink: boolean = false

  public dashboard: MenuLinkDTO = {
    text: ApplicationUrls.Dashboard.text,
    path: ApplicationUrls.Dashboard.path,
    icon: faHouse
  };

  @Output("onToggleFavorite")
  public onToggleFavorite = new EventEmitter<MenuLinkDTO>()

  @Output("sort")
  public onSortEmitter = new EventEmitter<MenuLinkDTO>();

  public isCollapsed$: Observable<boolean> = this.mainMenuService.collapsed$;

  constructor(
    private mainMenuService: MainMenuService,
    private cd: ChangeDetectorRef
  ) { }

  ngOnInit(): void {
  }

  /* RTE problem: Expression has changed after it was checked
   * http://www.syncfusion.com/forums/132152/rte-problem-expression-has-changed-after-it-was-checked?reply=ls7keM */
  ngAfterViewChecked() {
    this.cd.detectChanges();
  }

  onClick($event: any, link?: MenuLinkDTO) {
    if (link != undefined && link != null) {
      this.onToggleFavorite.emit(link);
      $event.stopPropagation();
      $event.preventDefault();
    } else {
      this.mainMenuService.setCollapsed(true);
    }
  }

  isExternal = (path: string) => path.includes('http');

  isFavorite = (link: MenuLinkDTO) => this.favorites.some(f => f.path == link.path);

  drop($event: any) {
    this.draggingLink = false;

    if ($event.previousIndex == $event.currentIndex) return;

    const nextOrder = $event.currentIndex + 1;
    const link = $event.items[0] as MenuLinkDTO;
    link.order = nextOrder;

    this.onSortEmitter.emit(link);
  }

  checkDrag($event: any) {
    if (!this.draggingLink) $event.cancel = true;
  }

  onDraggingLink() {
    this.draggingLink = true;
  }
}
