import { Injectable } from "@angular/core";
import { ServiceResultDTO } from "../interfaces/service-result/service-result.dto";
import { ValidationErrorDTO } from "../interfaces/service-result/validation-error.dto";
import { ConfirmDialogService } from "../../shared/services/confirm-dialog.service";
import { ToastService } from "../../shared/services/toast.service";

@Injectable()
export class ResultHandlerService {
  constructor(
    private confirmDialogService: ConfirmDialogService,
    private toastService: ToastService
  ) { }

  async handle<T>(result: ServiceResultDTO<T>): Promise<boolean> {
    if (result.isSuccess && result.successMessage != null && result.successMessage != '') {
      this.handleSuccessMessage(result);
    } else if (result.validationErrors != null && result.validationErrors.length > 0) {
      return await this.handleValidations(result);
    } else if (result.errors != null && result.errors.length > 0) {
      this.handleErrors(result);
      return false;
    }

    return true;
  }

  private handleSuccessMessage = <T>(result: ServiceResultDTO<T>) => this.toastService.show({ content: result.successMessage, icon: 'success' });

  private handleErrors = <T>(result: ServiceResultDTO<T>) => {
    this.toastService.show({ content: 'An internal error has occurred. Please contact the adminstrator.', icon: 'error' });
  } 

  private async handleValidations<T>(result: ServiceResultDTO<T>): Promise<boolean> {
    const errors = result.validationErrors.filter(v => v.severity.toString() == 'Error');
    if (errors.length > 0) {
      this.handleValidationErrors(errors);
      return false;
    }

    const infos = result.validationErrors.filter(v => v.severity.toString() == 'Info');
    if (infos.length > 0) {
      this.handleValidationInfos(infos);
      return true;
    }

    const warnings = result.validationErrors.filter(v => v.severity.toString() == 'Warning');
    if (warnings.length > 0) {
      return await this.handleValidationWarnings(warnings);
    }

    return true;
  }

  private handleValidationErrors = (errors: ValidationErrorDTO[]) => this.toastService.show({ content: errors.map(e => e.errorMessage).join('\n'), icon: 'error' });

  private handleValidationInfos = (infos: ValidationErrorDTO[]) => this.toastService.show({ content: infos.map(e => e.errorMessage).join('\n'), icon: 'info' });

  private async handleValidationWarnings(warnings: ValidationErrorDTO[]): Promise<boolean> {
    return await this.confirmDialogService.show({
      content: warnings.map(e => e.errorMessage).join('<br />'),
      requireAcknowledge: warnings.filter(w => w.identifier == 'DeactivateAndArchive').length > 0
    })
  }
}
