import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import { finalize, Observable } from 'rxjs';
import { AccountService } from '../services/account.service';
import { WPUserAccountDTO } from '../interfaces/user/user-account.dto';
import { GaitService } from '../services/gait.service';

@Injectable()
export class GaitInterceptor implements HttpInterceptor {

  constructor(private accountService: AccountService,
    private gaitService: GaitService) { }

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    let currentUser: WPUserAccountDTO | void = this.accountService.getCurrentUser(true)!;
    if (currentUser && currentUser.contextId) {
      this.gaitService.refreshContextId(currentUser.contextId);
    }

    return next.handle(request);
  }
}
