import { ResourceCenterListResolver } from './resource-center/resolvers/resource-center.resolver';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './authentication/guards/auth.guard';
import { RoleGuard } from './authentication/guards/role.guard';
import { UserTypeGuard } from './authentication/guards/user-type.guard';
import { AppContainerUnauthenticatedComponent } from './core/components/app-container-unauthenticated/app-container-unauthenticated.component';
import { AppContainerComponent } from './core/components/app-container/app-container.component';
import { RoleUtils } from './core/utils/role.utils';
import { DashboardComponent } from './dashboard/components/dashboard.component';
import { ToolsList } from './shared/constants/tools-list.enum';
import { ResourceCenterComponent } from './resource-center/components/resource-center-list/resource-center.component';
import { ToolListResolver } from './shared/resolvers/tool-list.resolver';
import { ToolActiveInRegionGuard } from './shared/guards/tool-active-region.guard';
import { Carrier411VisibleInRegionGuard } from './carrier411/front-end/guards/carrier411-visible-region.guard';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./authentication/authentication.module').then(m => m.AuthenticationModule),
    pathMatch: 'full',
    component: AppContainerUnauthenticatedComponent
  },
  {
    path: 'account',
    loadChildren: () => import('./authentication/authentication.module').then(m => m.AuthenticationModule),
    component: AppContainerUnauthenticatedComponent
  },
  {
    path: '',
    runGuardsAndResolvers: 'always',
    canActivate: [AuthGuard],
    children: [
      { path: 'dashboard', component: DashboardComponent },
      {
        path: 'admin',
        loadChildren: () => import('./admin/admin.module').then(m => m.AdminModule),
        canActivate: [RoleGuard],
        data: { roles: RoleUtils.getToolRoles(ToolsList.GlobalToolSuite) }
      },
      {
        path: 'carrier411-admin',
        loadChildren: () => import('./carrier411/admin/carrier411-admin.module').then(m => m.Carrier411AdminModule),
        canActivate: [ToolActiveInRegionGuard, RoleGuard],
        data: { toolId: ToolsList.Carrier411, roles: RoleUtils.getToolRoles(ToolsList.Carrier411) }
      },
      {
        path: 'carrier411',
        loadChildren: () => import('./carrier411/front-end/carrier411.module').then(m => m.Carrier411Module),
        canActivate: [Carrier411VisibleInRegionGuard, ToolActiveInRegionGuard, UserTypeGuard],
        data: { toolId: ToolsList.Carrier411 }
      },
      {
        path: 'resource-center',
        component: ResourceCenterComponent,
        resolve: { tools: ToolListResolver, resourceCenter: ResourceCenterListResolver }
      }
    ],
    component: AppContainerComponent
  }
  //TODO: these are for future use
  //{ path: '404', component: NotFoundComponent },
  //{ path: '**', redirectTo: '/404', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { onSameUrlNavigation: 'reload' })],
  exports: [RouterModule]
})

export class AppRoutingModule { }
