import { Component, OnInit } from '@angular/core';
import { DefaultButtonComponent } from '../default-button/default-button.component';
import { faArrowsSpin } from '@fortawesome/pro-regular-svg-icons';

@Component({
  selector: 'restore-button',
  templateUrl: '../default-button/default-button.component.html'
})
export class RestoreButtonComponent extends DefaultButtonComponent implements OnInit {
  constructor() {
    super();

    this.buttonIcon = faArrowsSpin;
    if (this.text == '') this.text = 'Restore';
  }

  ngOnInit(): void {
  }
}
