import { AfterViewChecked, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { faGlobeAmericas } from '@fortawesome/pro-light-svg-icons';
import { RegionDTO } from 'src/app/admin/region-management/interfaces/region.dto';
import { RegionService } from 'src/app/admin/region-management/services/region.service';
import { AccountService } from 'src/app/authentication/services/account.service';
import { RegionSelectorService } from '../../services/region-selector.service';
import { ResultHandlerService } from '../../services/result.handler.service';
import { UserSettingsService } from '../../services/user-settings.service';

@Component({
  selector: 'region-selector',
  templateUrl: './region-selector.component.html',
  styleUrls: ['./region-selector.component.scss'],
  providers: [RegionService]
})
export class RegionSelectorComponent implements OnInit, AfterViewChecked {
  faGlobeAmericas = faGlobeAmericas;
  public fields: Object = { text: 'name', value: 'id' };

  public regions!: RegionDTO[];
  public userRegionId!: number;

  constructor(
    private resultHandler: ResultHandlerService,
    private regionService: RegionService,
    private regionSelectorService: RegionSelectorService,
    private accountService: AccountService,
    private userSettingsService: UserSettingsService,
    private cd: ChangeDetectorRef
  ) {
  }

  ngOnInit(): void {
    this.loadUserRegion();
  }

  /* RTE problem: Expression has changed after it was checked
   * http://www.syncfusion.com/forums/132152/rte-problem-expression-has-changed-after-it-was-checked?reply=ls7keM */
  ngAfterViewChecked() {
    this.cd.detectChanges();
  }

  onRegionChanged(event: any) {
    const region = event.itemData;
    this.userRegionId = region.id;
    if (region != null) this.regionSelectorService.changeRegion(region);

    const user = this.accountService.getCurrentUser();
    if (user != null) {
      const userId = Number(user!.id);
      this.userSettingsService.updateUserRegionSettings(userId, user?.accountId, region.id).subscribe((data) => {
        if (data.isSuccess) {
          window.location.reload();
        } else {
          this.resultHandler.handle(data);
        }
      });
    }
  }

  loadUserRegion() {
    this.userSettingsService.userSettings$?.subscribe((data) => {
      if (data != null && this.userRegionId != data.regionId) {
        this.userRegionId = data.regionId;
        this.loadRegions();
      }
    });
  }

  loadRegions() {
    this.regionService.getActiveRegions().subscribe((data) => {
      if (data.isSuccess) {
        this.regions = data.value;
        this.publishUserRegion();
      } else {
        this.resultHandler.handle(data);
      }
    });
  }

  publishUserRegion() {
    if (this.regions != null) {
      const region = this.regions.filter(r => r.id == this.userRegionId)[0];
      if (this.regionSelectorService.regionSource.getValue() == null) {
        this.regionSelectorService.changeRegion(region);
      }
    }
  }
}
