import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';

export type Theme = 'warner-dark' | 'warner-light' | null;

@Injectable({
  providedIn: 'root'
})
export class ThemeService {
  private _themeSubject = new BehaviorSubject<[Theme, Theme]>([null, 'warner-dark']);
  theme$ = this._themeSubject.asObservable();

  private _isDarkSubject = new BehaviorSubject<boolean>(true);
  isDark$ = this._isDarkSubject.asObservable();

  activeTheme$ = this.theme$.pipe(
    map(theme => theme[1])
  );

  constructor() {
  }

  setTheme(theme: Theme) {
    this._themeSubject.next([this._themeSubject.getValue()[1], theme]);
    this._isDarkSubject.next(theme == 'warner-dark');
  }
}
