import { Component, Input, OnInit } from '@angular/core';
import { ItemDTO } from '../../interfaces/item.dto';

@Component({
  selector: 'option-list',
  templateUrl: './option-list.component.html',
  styleUrls: ['./option-list.component.scss']
})
export class OptionListComponent implements OnInit {
  public options?: ItemDTO[] = undefined;

  public title: string = '';

  @Input()
  public selectedIds: number[] | undefined = [];
  @Input()
  public padding: boolean = true;
  @Input()
  public rowInTitle: boolean = false;

  constructor() {
  }

  ngOnInit(): void {
  }

  loadOptions(options: any[]): void {
    this.options = options.map(r => {
      return {
        id: r.id,
        name: r.name,
        selected: this.selectedIds != null && this.selectedIds.findIndex(sr => sr == r.id) != -1
      }
    });
  }

  getSelectedIds = (): number[] => this.options?.filter(o => o.selected).map(o => o.id) ?? [];

  clear = () => this.options?.forEach(o => { o.selected = false; });

  reset() {
    if (this.selectedIds == null) {
      this.clear();
    } else {
      this.options?.forEach(o => { o.selected = this.selectedIds?.findIndex(sr => sr == o.id) != -1; });
    }
  }
}
