import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../../../shared/interfaces/tools/tool.dto';
import { ToolCreateDTO } from '../interfaces/tool-create.dto';
import { ToolManagementUpdateDTO } from '../../../shared/interfaces/tools/tool-management-update.dto';
import { ToolUpdateDTO } from '../interfaces/tool-update.dto';
import { ToolUpdatePermissionsDTO } from '../interfaces/tool-update-permissions.dto';
import { ToolApiService } from './tool.api.service';
import { CarrierDTO } from '../../carrier-management/interfaces/carrier.dto';
import { ToolMessagesDTO } from 'src/app/shared/interfaces/tools/tool-messages.dto';

@Injectable({
  providedIn: 'root'
})
export class ToolService {

  constructor(private toolApiService: ToolApiService) { }

  public getManageableTools(isActive: boolean): Observable<ServiceResultDTO<ToolDTO[]>> {
    return this.toolApiService.getManageableTools(isActive);
  }

  public getToolPermissionGroups(id: number): Observable<ServiceResultDTO<number[]>> {
    return this.toolApiService.getToolPermissionGroups(id);
  }

  public isToolActiveInCurrentRegion(toolId: number) {
    return this.toolApiService.isToolActiveInCurrentRegion(toolId);
  }

  public getToolActiveCarriers(id: number): Observable<ServiceResultDTO<CarrierDTO[]>> {
    return this.toolApiService.getToolActiveCarriers(id);
  }

  public createTool(tool: ToolCreateDTO, primaryLogo?: File, fullSizeLogo?: File): Observable<ServiceResultDTO<ToolDTO>> {
    return this.toolApiService.createTool(tool, primaryLogo, fullSizeLogo);
  }

  public validateUpdate(id: number, tool: ToolUpdateDTO, primaryLogo?: File): Observable<ServiceResultDTO<boolean>> {
    return this.toolApiService.validateUpdate(id, tool, primaryLogo);
  }

  public updateTool(id: number, tool: ToolUpdateDTO, primaryLogo?: File, fullSizeLogo?: File): Observable<ServiceResultDTO<ToolDTO>> {
    return this.toolApiService.updateTool(id, tool, primaryLogo, fullSizeLogo);
  }

  public validateUpdateGeneralInfo(id: number, tool: ToolUpdateDTO, primaryLogo?: File): Observable<ServiceResultDTO<boolean>> {
    return this.toolApiService.validateUpdateGeneralInfo(id, tool, primaryLogo);
  }

  public updateGeneralInfo(id: number, tool: ToolUpdateDTO, primaryLogo?: File, fullSizeLogo?: File): Observable<ServiceResultDTO<ToolDTO>> {
    return this.toolApiService.updateGeneralInfo(id, tool, primaryLogo, fullSizeLogo);
  }

  public validateUpdatePermissions(id: number, tool: ToolUpdatePermissionsDTO): Observable<ServiceResultDTO<boolean>> {
    return this.toolApiService.validateUpdatePermissions(id, tool);
  }

  public updatePermissions(id: number, tool: ToolUpdatePermissionsDTO): Observable<ServiceResultDTO<ToolDTO>> {
    return this.toolApiService.updatePermissions(id, tool);
  }

  public validateUpdateToolManagement(id: number, toolManagement: ToolManagementUpdateDTO[]): Observable<ServiceResultDTO<boolean>> {
    return this.toolApiService.validateUpdateToolManagement(id, toolManagement);
  }

  public updateToolManagement(id: number, toolManagement: ToolManagementUpdateDTO[]): Observable<ServiceResultDTO<ToolDTO>> {
    return this.toolApiService.updateToolManagement(id, toolManagement);
  }

  public updateToolOrder(id: number, newOrder: number): Observable<ServiceResultDTO<boolean>> {
    return this.toolApiService.updateToolOrder(id, newOrder);
  }

  public validateDelete(tool: ToolDTO): Observable<ServiceResultDTO<boolean>> {
    return this.toolApiService.validateDelete(tool);
  }

  public delete(tool: ToolDTO): Observable<ServiceResultDTO<boolean>> {
    return this.toolApiService.delete(tool.id);
  }

  public validateRestore(tool: ToolDTO): Observable<ServiceResultDTO<boolean>> {
    return this.toolApiService.validateRestore(tool);
  }

  public restoreTool(tool: ToolDTO): Observable<ServiceResultDTO<boolean>> {
    return this.toolApiService.restoreTool(tool.id);
  }

  public updateWelcomeMessage(toolId: number, welcomeMessage: string): Observable<ServiceResultDTO<string>> {
    return this.toolApiService.updateWelcomeMessage(toolId, welcomeMessage);
  }

  public updateFavoriteMessage(toolId: number, favoriteMessage: string): Observable<ServiceResultDTO<string>> {
    return this.toolApiService.updateFavoriteMessage(toolId, favoriteMessage);
  }

  public updateAnnouncementMessage(toolId: number, welcomeMessage: string): Observable<ServiceResultDTO<string>> {
    return this.toolApiService.updateAnnouncementMessage(toolId, welcomeMessage);
  }

  public getToolMessages(toolId: number): Observable<ServiceResultDTO<ToolMessagesDTO>> {
    return this.toolApiService.getToolMessages(toolId);
  }
}
