import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { TextBoxComponent } from '@syncfusion/ej2-angular-inputs';
import { faPlus } from '@fortawesome/pro-regular-svg-icons';
import { faBoxArchive } from '@fortawesome/pro-regular-svg-icons';
import { RestrictedComponent } from '../restricted/restricted.component';

@Component({
  selector: 'container-list',
  templateUrl: './container-list.component.html',
  styleUrls: ['./container-list.component.scss']
})
export class ContainerListComponent extends RestrictedComponent implements OnInit {
  public showArchived: boolean = false;
  public archiveIcon = faBoxArchive;
  public actionButtonIcon = faPlus;

  @Input()
  public handleArchive: boolean = true;

  @Input()
  public showActionButton: boolean | undefined = true;

  @Input()
  public title: string = '';
  @Input()
  public actionButtonText: string = '';
  @Input()
  public actionButtonLink: string = '';

  @Input()
  public searchPlaceHolder: string = 'Search by name...';

  @ViewChild('searchTextbox')
  public searchTextbox!: TextBoxComponent;

  @Output('search')
  onSearchTrigger = new EventEmitter<any>();

  @Output('archiveChange')
  onArchiveChangeTrigger = new EventEmitter<boolean>();

  @Output('actionClick')
  onActionClickTrigger = new EventEmitter();

  constructor() {
    super();
  }

  ngOnInit(): void {
  }

  onCreate(args: any): void {
    this.searchTextbox.addIcon("append", "e-icons e-search");
  }

  onSearch(args: any): void {
    this.onSearchTrigger.emit(args);
  }

  onArchiveChange(): void {
    this.showArchived = !this.showArchived;
    this.onArchiveChangeTrigger.emit(this.showArchived);
  }

  onActionClick(): void {
    this.onActionClickTrigger.emit();
  }
}
