import { Injectable } from '@angular/core';
import Swal, { SweetAlertIcon, SweetAlertOptions } from 'sweetalert2';

@Injectable()
export class ConfirmDialogService {
  constructor() { }

  public async show(options: {
    content: string,
    title?: string,
    cancelText?: string,
    confirmText?: string,
    icon?: SweetAlertIcon,
    requireAcknowledge?: boolean
  }): Promise<boolean> {

    const toastConfig: SweetAlertOptions = {
      title: options.title ?? '<label class="fw-bold red-color">Are you sure?</label>',
      html: options.content! + '<br />Would you still like to continue?',
      icon: options.icon ?? undefined,
      showCancelButton: true,
      showCloseButton: true,
      cancelButtonText: options.cancelText ?? 'Cancel',
      confirmButtonColor: '#3085d6',
      confirmButtonText: options.confirmText ?? 'Continue',
      reverseButtons: true
    };

    if (options.requireAcknowledge) {
      toastConfig.input = 'checkbox';
      toastConfig.inputValue = 0;
      toastConfig.inputPlaceholder = 'I acknowledge that this action will affect data globally.';
      toastConfig.inputValidator = (accept) => {
        return new Promise((resolve) => {
          if (!accept) {
            resolve('You need to acknowledge');
          } else {
            resolve(null);
          }
        })
      };
      toastConfig.customClass = {
        container: 'confirm-dialog-container',
        input: 'acknowledge-action'
      };

      const { value: accept } = await Swal.fire(toastConfig);
      return accept;
    } else {
      const { value: accept } = await Swal.fire(toastConfig);
      return accept;
    }
  }
}
