import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { ToolService } from 'src/app/admin/tools-management/services/tool.service';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';

@Component({
  selector: 'welcome-message-edit',
  templateUrl: './welcome-message-edit.component.html',
  styleUrls: ['./welcome-message-edit.component.scss']
})
export class WelcomeMessageEditComponent implements OnInit, OnChanges {
  @Input("title")
  public title: string = "Welcome Message Management";
  @Input("description")
  public description: string = "";
  @Input("toolId")
  public toolId: number = 0;
  @Input('welcomeMessage')
  public welcomeMessage: string | undefined = "";
  public newMessage: string = "";

  constructor(
    private toolService: ToolService,
    private resultHandlerService: ResultHandlerService
  ) { }

  ngOnInit(): void {
  }

  ngOnChanges(): void {
    this.newMessage = this.welcomeMessage!;
  }

  onUndo() {
    this.newMessage = this.welcomeMessage!;
  }

  onSave() {
    this.toolService.updateWelcomeMessage(this.toolId, this.newMessage).subscribe((data) => {
      this.resultHandlerService.handle(data).then((success) => {
        if (success) this.welcomeMessage = data.value;
      });
    });
  }
}
