import { Directive, Input, OnInit, TemplateRef, ViewContainerRef } from '@angular/core';
import { StringUtils } from 'src/app/shared/utilities/string-utils';
import { WPUserAccountDTO } from '../../authentication/interfaces/user/user-account.dto';
import { AccountService } from '../../authentication/services/account.service';
import { RegionSelectorService } from '../services/region-selector.service';

@Directive({
  selector: '[appHasRole]'
})
export class HasRoleDirective implements OnInit {
  @Input()
    appHasRole!: string[];
  user: WPUserAccountDTO;
  constructor(private viewContainerRef: ViewContainerRef,
    private templateRef: TemplateRef<any>,
    private accountService: AccountService,
    private regionSelectorService: RegionSelectorService) {
    this.user = this.accountService.getCurrentUser() as WPUserAccountDTO;
  }

  ngOnInit(): void {
    //clear view if no roles
    if (!this.user?.roles || this.user == null) {
      this.viewContainerRef.clear();
      return;
    }

    this.appHasRole = this.handleRegionOnRoles(this.appHasRole);

    if (this.user?.roles.some(r => this.appHasRole.includes(r))) {
      this.viewContainerRef.createEmbeddedView(this.templateRef);
    } else {
      this.viewContainerRef.clear();
    }
  }

  private handleRegionOnRoles(roles: string[]) {
    const currentRegion = this.regionSelectorService.getCurrentAbbreviation();

    return roles.map((role) => {
      return StringUtils.format(role, '.', currentRegion);
    });
  }

}
