import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { RegionSelectorService } from 'src/app/core/services/region-selector.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import { StringUtils } from 'src/app/shared/utilities/string-utils';
import { AccountService } from '../services/account.service';

@Injectable({
  providedIn: 'root'
})
export class RoleGuard implements CanActivate {
  public static separator: string = ".";

  constructor(
    private router: Router,
    private accountService: AccountService,
    private regionSelectorService: RegionSelectorService,
    private toastService: ToastService
  ) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {    
    var requiredRoles = route.data["roles"];
    if (!(requiredRoles instanceof Array) || requiredRoles.length === 0) {
      return true;
    }

    requiredRoles = this.handleRegionOnRoles(requiredRoles);

    const user = this.accountService.getCurrentUser();
    const hasRequiredRole = requiredRoles.some((role: string) => user?.roles?.includes(role));
    if (!hasRequiredRole) {
      this.router.navigate(['/']);
      this.toastService.show({ content: 'Access denied!', icon: 'error' });
    }

    return hasRequiredRole;
  }

  private handleRegionOnRoles(roles: string[]) {
    const currentRegion = this.regionSelectorService.getCurrentAbbreviation();

    return roles.map((role) => {
      return StringUtils.format(role, '.', currentRegion);
    });
  }
}
