import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { RegionApiService } from './regions.api.service';
import { RegionDTO } from '../interfaces/region.dto';
import { RegionUpdateDTO } from '../interfaces/region.update.dto';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';

@Injectable()
export class RegionService {

  constructor(private regionApiService: RegionApiService) { }

  public getActiveRegions(): Observable<ServiceResultDTO<RegionDTO[]>> {
    return this.regionApiService.getWithFilter(undefined, true);
  }

  public update(regions: RegionUpdateDTO[]): Observable<ServiceResultDTO<boolean>> {
    return this.regionApiService.update(regions);
  }

  public validateUpdate(regions: RegionUpdateDTO[]): Observable<ServiceResultDTO<boolean>> {
    return this.regionApiService.validateUpdate(regions);
  }
}
