import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { Location } from '@angular/common';
import { UserChangePasswordDTO } from "../../../admin/user-management/interfaces/user-change-password.dto";
import { UserService } from "../../../admin/user-management/services/user.service";
import { ResultHandlerService } from "../../../core/services/result.handler.service";
import { InputValidatorService } from "../../../shared/services/input-validator.service";
import { AccountService } from "../../services/account.service";
import { ResetPasswordDTO } from "../../interfaces/account/reset-password.dto";


@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {
  form!: FormGroup;
  public token: string;
  public email: string;

  constructor(
    private fb: FormBuilder,
    public inputValidatorService: InputValidatorService,
    private route: ActivatedRoute,
    private accountService: AccountService,
    private resultHandler: ResultHandlerService) {
    this.token = this.route.snapshot.queryParams['token'];
    this.email = this.route.snapshot.queryParams['email'];

    if (!this.token || !this.email) {
      this.accountService.logout();
    }
  }

  ngOnInit() {
    this.initializeForm();
  }

  initializeForm() {
    this.form = this.fb.group({
      password: ['', Validators.required],
      confirmPassword: ['', Validators.required]
    });
  }

  public resetPassword() {
    this.form.markAllAsTouched();
    this.form.markAsDirty();
    const resetPassFormValues = this.form.value;
    const resetPassDto: ResetPasswordDTO = {
      password: resetPassFormValues.password,
      confirmPassword: resetPassFormValues.confirmPassword,
      token: this.token,
      email: this.email
    }

    this.accountService.resetPassword(resetPassDto).subscribe(
      (data) => { this.resultHandler.handle(data) },
      (err) => { this.resultHandler.handle(err.error); });
  }
}
