import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { AccountService } from '../../authentication/services/account.service';
import { GaitService } from '../../authentication/services/gait.service';
import { ApplicationUrls } from '../../shared/constants/application-urls';
import { ResultHandlerService } from './result.handler.service';

export function appStartupServiceFactory(startupService: AppStartupService) {
  return () => startupService.load();
}

@Injectable()
export class AppStartupService {
  constructor(private accountService: AccountService,
    private gaitService: GaitService,
    private resultHandler: ResultHandlerService) {
  }

  load(): Promise<any> {
    return new Promise((resolve, reject) => {
      if (this.isOnAccountPage()) { return resolve(null); }

      const hasValidAuthSession = this.accountService.isValidAuthPersisted();

      if (hasValidAuthSession) {
        return resolve(null);
      } else {
        let gaitContextId = this.getGaitContextIdFromUrl();
        if (!gaitContextId) {
          this.goToLogin();
        }
        else {
          this.processGaitContext(gaitContextId);
        }
      }
    });
  }

  public isOnAccountPage() {
    return window.location.pathname == "/"
      || window.location.pathname == ApplicationUrls.Account.ForgotPassword.path
      || window.location.pathname == ApplicationUrls.Account.ResetPassword.path;
  }

  private goToLogin() {
    if (this.isOnAccountPage()) { return; }

    this.redirectToMarketingLoginPage();
  }

  private logOut() {
    this.accountService.logout();
  }

  private getGaitContextIdFromUrl() {
    return this.getParameterByName('context');
  }

  private processGaitContext(contextId: string) {
    if (contextId == '') {
      this.logOut();
    }
    else {
      //try to authenticate in gait
      this.gaitService.authenticateGaitUser(contextId).subscribe((data) => {
        this.resultHandler.handle(data).then((success) => {
          if (success) {
            let gaitUser = data.value;
            this.accountService.saveUser(gaitUser);
            this.redirectToReturnUrlOrDashboard();
          }
        });
      }, (err) => {
        this.resultHandler.handle(err.error);
      });
    }
    //if successfully authenticated, store a cookie and redirect to return url
    //else redirect to the marketing site
    // not logged in, redirect to login page with the return url and return false
    //this.router.navigate(['/'], { queryParams: { returnUrl: state.url } });
  }

  private redirectToMarketingLoginPage() {
    let loginUrl = environment.marketing.baseUrl + '/login'
    if (window.location.pathname != loginUrl)
      loginUrl += '?returnUrl=' + window.location.href;

    window.location.href = loginUrl;
  }

  private redirectToReturnUrlOrDashboard() {
    var returnUrl = this.getParameterByName('returnUrl');
    if (returnUrl)
      window.location.href = returnUrl;
    else
      window.location.href = ApplicationUrls.Dashboard.path;
  }

  private getParameterByName(name: string, url = window.location.href) {
    name = name.replace(/[\[\]]/g, '\\$&');
    var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)'),
      results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, ' '));
  }
}
