import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ResultHandlerService } from '../../../core/services/result.handler.service';
import { InputValidatorService } from '../../../shared/services/input-validator.service';
import { getLoginFormGroup } from '../../forms/login-form-group';
import { AccountService } from '../../services/account.service';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  public form!: FormGroup;
  public bypassingLogin = true; // So the login component doesn't flash on screen before the redirect when authenticated
  returnUrl!: string;
  public forgotPasswordUrl = ApplicationUrls.Account.ForgotPassword.path;

  constructor(private accountService: AccountService,
    private route: ActivatedRoute,
    private router: Router,
    private resultHandler: ResultHandlerService,
    public inputValidatorService: InputValidatorService) {
    this.checkIfUserLoggedIn();
  }

  private checkIfUserLoggedIn() {
    if (!this.accountService.isValidAuthPersisted()) {
      this.bypassingLogin = false;
      return;
    }

    this.bypassingLogin = true;
    this.navigateAfterLogin();
  }

  ngOnInit(): void {
    this.form = getLoginFormGroup();
  }

  login() {
    if (!this.form.valid) {
      return;
    }

    this.accountService.login(this.form.value).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.navigateAfterLogin();
        }
      });
    }, (err) => {
      this.resultHandler.handle(err.error);
    });
  }

  private navigateAfterLogin() {
    // get return url from route parameters or default to ''
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || ApplicationUrls.Dashboard.path;
    this.router.navigateByUrl(this.returnUrl);
  }
}
