import { Injectable } from "@angular/core";
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from "@angular/router";
import { Observable } from "rxjs";
import { ServiceResultDTO } from "src/app/core/interfaces/service-result/service-result.dto";
import { ResourceCenterDTO } from "../../shared/interfaces/resource-center/resource-center.dto";
import { SharedApiService } from "../../shared/services/shared.api.service";

@Injectable()
export class ResourceCenterListResolver implements Resolve<ServiceResultDTO<ResourceCenterDTO>> {

  constructor(private resourceCenterService: SharedApiService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<ResourceCenterDTO>> {
    return this.resourceCenterService.getResourceCenter();
  }
}
