import { enableProdMode } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';

import { AppModule } from './app/app.module';
import { environment } from './environments/environment';

import { registerLicense } from '@syncfusion/ej2-base';
import { RetentionPeriod } from './app/core/logger/constants/logger.enums';
import Logger from './app/core/logger/services/logger.service';

const providers = [
  { provide: 'BASE_API_URL_WEB', useFactory: getBaseAPIURL_Web, deps: [] as any[] }
];

if (environment.production) {
  enableProdMode();
}

registerLicense(environment.syncFusionApiKey)

const cloudwatch = environment.cloudwatch;
if (cloudwatch.isEnable) {
  const logger = new Logger(cloudwatch.accessKeyId, cloudwatch.secretAccessKey, cloudwatch.region, cloudwatch.logGroupName);
  logger.install({
    logStreamNameResolver() {
      return environment.cloudwatch.logStreamName;
    },
    retentionPeriod: (<any>RetentionPeriod)[cloudwatch.retentionPeriod]
  })
}

platformBrowserDynamic(providers).bootstrapModule(AppModule)
  .catch(err => console.error(err));

/* Provider Factories */
function getBaseAPIURL_Web() {
  return environment.api.web;
}
