import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { RouterModule } from '@angular/router';
import { AuthenticationModule } from "../authentication/authentication.module";
import { SharedModule } from "../shared/shared.module";
import { AppContainerComponent } from './components/app-container/app-container.component';
import { AppContainerUnauthenticatedComponent } from './components/app-container-unauthenticated/app-container-unauthenticated.component';
import { AppHeaderComponent } from './components/app-header/app-header.component';
import { BreadcrumbComponent } from './components/breadcrumb/breadcrumb.component';
import { MainMenuComponent } from "./components/main-menu/main-menu.component";
import { NavbarComponent } from "./components/navbar/navbar.component";

// Region Selector
import { RegionSelectorComponent } from './components/region-selector/region-selector.component';
import { CurrentRegionInterceptor } from "./interceptors/current-region.interceptor";
import { HTTP_INTERCEPTORS } from "@angular/common/http";
import { UserSettingsComponent } from './components/user-settings/user-settings.component';
import { MainMenuLinksComponent } from './components/main-menu-links/main-menu-links.component';

@NgModule({
  declarations: [
    AppContainerComponent,
    AppContainerUnauthenticatedComponent,
    MainMenuComponent,
    NavbarComponent,
    AppHeaderComponent,
    BreadcrumbComponent,
    RegionSelectorComponent,
    UserSettingsComponent,
    MainMenuLinksComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    AuthenticationModule,
    RouterModule,
    SharedModule
  ],
  exports: [
    AppContainerComponent,
    AppContainerUnauthenticatedComponent,
    MainMenuComponent,
    NavbarComponent
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: CurrentRegionInterceptor,
      multi: true,
    }    
  ]
})
export class CoreModule { }
