import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { FileManagerApiService } from './file-manager.api.service';

@Injectable()
export class FileManagerService {

  constructor(private fileManagerApiService: FileManagerApiService) { }

  public getFromFullPath(fullFilePath: string): Observable<Blob> {
    return this.fileManagerApiService.downloadFromFullPath(fullFilePath);
  }
}
