import { AbstractControl } from "@angular/forms";

export abstract class CustomValidators {
  public static urlRegex: RegExp = /^(?:http(s?):\/\/)[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\(\)\*\+,;=.]+$/;
  public static emailRegex: RegExp = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;

  public static multipleEmailsValidator(control: AbstractControl): { [key: string]: boolean } | null {
    if (control.value !== undefined && control.value != '') {
      const emails = (control.value as string)?.split(',');
      if (emails?.length > 0) {
        const invalidEmails = emails.filter(e => !CustomValidators.emailRegex.test(e.trim()));
        return { 'validEmails': invalidEmails.length == 0 };
      }
    }

    return null;
  }
}
