import { HttpClient } from '@angular/common/http';
import { Injectable, Inject } from '@angular/core';
import { Observable } from 'rxjs';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { AdminApiEndpoints } from '../../constants/admin-api.constants';

@Injectable({
  providedIn: "root"
})
export class FileManagerApiService extends BaseAPIService {
  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string,
    http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  public downloadFromFullPath(fullFilePath: string): Observable<Blob> {
    return this.http.get(
      this.constructRequestUrl(AdminApiEndpoints.DownloadFromFullPath(fullFilePath)),
      { responseType: 'blob' }
    );
  }
}
