import { Component, ElementRef, forwardRef, OnInit, ViewChild } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { RichTextEditorComponent, ToolbarSettingsModel } from '@syncfusion/ej2-angular-richtexteditor';
import { getDefaultRichTextFormat, getDefaultRichTextInlineMode, getDefaultRichTextSettings } from '../../utilities/shared';
import { UrlUtils } from '../../utilities/url-utils';

@Component({
  selector: 'rich-text-editor-with-file',
  templateUrl: './rich-text-editor-with-file.component.html',
  styleUrls: ['./rich-text-editor-with-file.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => RichTextEditorWithFileComponent)
    }
  ]
})
export class RichTextEditorWithFileComponent implements OnInit, ControlValueAccessor {
  text: string = '';
  public files: any[] = [];

  @ViewChild('attachment')
  public attachment!: ElementRef;
  @ViewChild('richTextEditor')
  public richTextEditor!: RichTextEditorComponent;

  public toolbarSettings: ToolbarSettingsModel = this.getToolbarSettings();
  public inlineMode: object = getDefaultRichTextInlineMode();
  public format: object = getDefaultRichTextFormat();

  private onTouched!: Function;
  private onChanged!: Function;

  constructor() { }

  ngOnInit(): void { }

  public initializeRichText(text: string) {
    this.richTextEditor.autoResize();
    this.richTextEditor.refreshUI();
    this.handleFiles(text);
  }

  public getFiles(): File[] {
    return this.files.filter(f => f.size > 0);
  }

  public handleChange($event: any) {
    if ($event.value) {
      this.onTouched();
      this.text = $event.value;
      this.onChanged(this.text);
      this.handleFiles(this.text);
    }
  }

  public writeValue(value: string) {
    this.text = value;
  }

  public registerOnChange(fn: any) {
    this.onChanged = fn;
  }

  public registerOnTouched(fn: any) {
    this.onTouched = fn;
  }

  public downloadFile(file: any): void {
    if (file.url) {
      window.open(file.url, '_blank')
    } else {
      var link = document.createElement("a");
      link.download = file.name;
      link.href = URL.createObjectURL(file);
      link.target = '_blank';
      link.click();
    }
  }

  public fileUploaded(event: Event): void {
    const file = this.addFile(event)
    if (file != null) {
      this.highlightTextWithFile(file.name);
      this.handleChange({ value: this.richTextEditor.getHtml() });
    }
  }

  private getCurrentNode(): any {
    const selectedRange = this.richTextEditor.getRange();

    return selectedRange.startContainer.nodeName === "#text" ?
      selectedRange.startContainer.parentElement as any :
      selectedRange.startContainer as any;
  }

  private getToolbarSettings(): ToolbarSettingsModel {
    const settings = getDefaultRichTextSettings();
    settings.items!.push('-');
    settings.items!.push({
      tooltipText: 'Attach related document',
      undo: true,
      click: () => {
        this.selectAttachment();
      },
      template: `<button class="e-tbar-btn e-btn" tabindex="-1" style="width:100%">
                <div class="e-icons e-changes-track" style="font-weight: 500;"></div></button>`
    });
    settings.items!.push({
      tooltipText: 'Detach related document',
      undo: true,
      click: () => {
        this.removeAttachment();
      },
      template: `<button class="e-tbar-btn e-btn" tabindex="-1" style="width:100%">
                <div class="e-icons e-changes-reject" style="font-weight: 500;"></div></button>`
    });

    return settings;
  }

  private addFile(event: Event): File | null {
    const element = event.currentTarget as HTMLInputElement;
    let fileList: FileList | null = element.files;
    if (fileList && fileList.length > 0 && fileList[0] != null) {
      const file = fileList[0];
      this.files.push(file);
      element.value = '';
      return file;
    }

    return null;
  }

  private selectAttachment(): void {
    this.attachment.nativeElement.click();
  }

  private removeAttachment(): void {
    const currentNode = this.getCurrentNode();
    if (currentNode) {
      if (currentNode.hasAttribute('file')) {
        currentNode.removeAttribute('file');
      } else if (currentNode.parentElement.hasAttribute('file')) {
        currentNode.parentElement.removeAttribute('file');
      }

      this.handleChange({ value: this.richTextEditor.getHtml() });
    }
  }

  private highlightTextWithFile(fileName: string): void {
    const currentNode = this.getCurrentNode();
    if (currentNode) {
      currentNode.setAttribute('file', fileName);
    }
  }

  private handleFiles(text: string): void {
    if (text == null) {
      this.files = [];
      return;
    }

    const fileNames = text.match(/(?<=file=").*?(?=">| ?")/gs);
    if (fileNames) {
      const files: any[] = []
      for (let fileName of fileNames) {
        const currentFile = this.files.filter(f => f.name.includes(fileName));
        if (currentFile && currentFile.length) {
          files.push(currentFile[0] as any);
        } else {
          files.push({
            name: UrlUtils.getPrettyFileName(fileName),
            url: fileName
          })
        }
      }

      this.files = files;
    } else {
      this.files = [];
    }
  }
}
