import { LinkDTO } from "src/app/shared/interfaces/link.dto";
import { NavBarDTO } from "../interfaces/nav-bar.dto";
import { ToolDTO } from "../../shared/interfaces/tools/tool.dto";
import { formatAdminToolName } from "../../shared/utilities/shared";
import { ApplicationUrls } from "../../shared/constants/application-urls";
import { UrlUtils } from "src/app/shared/utilities/url-utils";
import { Carrier411CategoryDTO } from "src/app/carrier411/shared/interfaces/carrier411-category.dto";

export function GlobalToolSuiteMenu(): NavBarDTO {
  return {
    returnUrl: ApplicationUrls.GlobalToolSuite.path,
    title: ApplicationUrls.GlobalToolSuite.text,
    links: UrlUtils.getLinksFromObject(ApplicationUrls.GlobalToolSuite)
  }
};

let navElements: NavElementType = {
  GetGlobalToolSuite: (): NavBarDTO => {
    return GlobalToolSuiteMenu()
  },
  GetUserManagement: (userType: string): NavBarDTO => {
    var links: LinkDTO[] = [];
    links.push(UrlUtils.getLinkFromObject(ApplicationUrls.GlobalToolSuite.UserManagement.ContactInfo)!);
    if (userType == 'Administrator') {
      links.push(UrlUtils.getLinkFromObject(ApplicationUrls.GlobalToolSuite.UserManagement.Security)!);
    }
    links.push(UrlUtils.getLinkFromObject(ApplicationUrls.GlobalToolSuite.UserManagement.Roles)!);

    return {
      showReturn: true,
      returnUrl: ApplicationUrls.GlobalToolSuite.UserManagement.path,
      title: 'User Settings',
      links: links
    }
  },
  GetRoleManagement: (tools: ToolDTO[]): NavBarDTO => {
    return {
      showReturn: true,
      returnUrl: ApplicationUrls.GlobalToolSuite.RoleManagement.path,
      title: ApplicationUrls.GlobalToolSuite.RoleManagement.text,
      links: tools.map(t => {
        return {
          text: formatAdminToolName(t),
          path: UrlUtils.replaceParams(ApplicationUrls.GlobalToolSuite.RoleManagement.RoleToolManagement.path, t.id)
        }
      })
    }
  },
  GetToolManagement: (tool: ToolDTO): NavBarDTO => {
    if (tool == null || tool?.isExternal) return GlobalToolSuiteMenu();

    var links: LinkDTO[] = [];
    if (tool?.isManagedByStateAndCarrier) {
      links.push(UrlUtils.getLinkFromObject(ApplicationUrls.GlobalToolSuite.ToolsManagement.StateAndCarrier)!);
    }

    links.push(UrlUtils.getLinkFromObject(ApplicationUrls.GlobalToolSuite.ToolsManagement.GeneralInformation)!);
    links.push(UrlUtils.getLinkFromObject(ApplicationUrls.GlobalToolSuite.ToolsManagement.Permission)!);

    return {
      showReturn: true,
      returnUrl: ApplicationUrls.GlobalToolSuite.ToolsManagement.path,
      title: tool?.name,
      links: links
    }
  },
  GetCarrier411Admin: () => {
    return {
      returnUrl: ApplicationUrls.Carrier411Admin.path,
      title: ApplicationUrls.Carrier411Admin.text,
      links: UrlUtils.getLinksFromObject(ApplicationUrls.Carrier411Admin)
    }
  }  
};

interface NavElementType {
  readonly GetGlobalToolSuite: Function,
  readonly GetUserManagement: Function,
  readonly GetRoleManagement: Function,
  readonly GetToolManagement: Function,
  readonly GetCarrier411Admin: Function
}

export const NavElements: NavElementType = navElements;
