import { ToolsList } from "src/app/shared/constants/tools-list.enum";
import { StringUtils } from "src/app/shared/utilities/string-utils";
import { Roles } from "../constants/role.constants";

export abstract class RoleUtils {
  public static separator: string = ".";

  public static getToolRoles(tool: ToolsList, currentRegion?: string): string[] {
    switch (tool) {
      case ToolsList.GlobalToolSuite:
        return [
          Roles.GlobalToolSuite,
          Roles.SuperAdmin
        ];
      case ToolsList.Carrier411:
        return [
          StringUtils.format(Roles.Carrier411.Editor, this.separator, currentRegion),
          StringUtils.format(Roles.Carrier411.Reviewer, this.separator, currentRegion),
          StringUtils.format(Roles.Carrier411.Publisher, this.separator, currentRegion)
        ];
      case ToolsList.TeamSheets:
        return [
          Roles.TeamSheets.TeamSheetsAdmin
        ];
      case ToolsList.LargeGroupRFP:
        return [
          StringUtils.format(Roles.LargeGroupRFP.Admin, this.separator, currentRegion),
          StringUtils.format(Roles.LargeGroupRFP.Assignee, this.separator, currentRegion)
        ];
      case ToolsList.DocumentToolkit:
        return [
          StringUtils.format(Roles.DocumentToolkit.Editor, this.separator, currentRegion),
          StringUtils.format(Roles.DocumentToolkit.Reviewer, this.separator, currentRegion),
          StringUtils.format(Roles.DocumentToolkit.Publisher, this.separator, currentRegion)
        ];
      default:
        return [];
    }
  }
}
