import { UserResourceCenterDTO } from '../interfaces/user-resource-center.dto';
import { Injectable } from '@angular/core';
import { BehaviorSubject, map, Observable, ReplaySubject, share, switchMap, timer } from 'rxjs';
import { AccountService } from 'src/app/authentication/services/account.service';
import { ServiceResultDTO } from '../../core/interfaces/service-result/service-result.dto';
import { FavoriteType } from '../constants/favorite-type';
import { UserFavoriteDTO } from '../interfaces/user-favorite.dto';
import { UserSettingsDTO } from '../interfaces/user-settings.dto';
import { UserSettingsApiService } from './user-settings.api.service';
import { ToolDTO } from 'src/app/shared/interfaces/tools/tool.dto';

@Injectable({
  providedIn: 'root'
})
export class UserSettingsService {
  private CACHE_TIMEOUT: number = 2000;
  private _userSettingsData = new BehaviorSubject<void>(undefined);
  public userSettings$!: Observable<UserSettingsDTO | null>;

  constructor(
    private userSettingsApiService: UserSettingsApiService,
    private accountService: AccountService
  ) {
    this.loadUserSettings();
  }

  toggleUserFavorite(
    userId: number,
    accountId: string | null,
    toolId: number,
    favoriteType: FavoriteType
  ): Observable<ServiceResultDTO<UserSettingsDTO>> {
    return this.userSettingsApiService.toggleUserFavorite(userId, accountId, toolId, favoriteType);
  }

  toggleUserResourceCenterFavorite(
    userId: number,
    toolId: number
  ): Observable<ServiceResultDTO<UserSettingsDTO>> {
    return this.userSettingsApiService.toggleUserResourceCenterFavorite(userId, toolId);
  }

  updateUserFavoriteOrder(
    userId: number,
    accountId: string | null,
    favorite: UserFavoriteDTO
  ): Observable<ServiceResultDTO<UserSettingsDTO>> {
    return this.userSettingsApiService.updateUserFavoriteOrder(userId, accountId, favorite);
  }

  updateUserResourceCenterOrder(
    userId: number,
    accountId: string | null,
    tool: UserResourceCenterDTO
  ): Observable<ServiceResultDTO<UserSettingsDTO>> {
    return this.userSettingsApiService.updateUserResourceCenterOrder(userId, accountId, tool);
  }

  updateUserMenuThemeSettings(
    userId: number,
    accountId: string | null,
    isDarkModeEnabled: boolean
  ): Observable<ServiceResultDTO<boolean>> {
    return this.userSettingsApiService.updateUserMenuThemeSettings(userId, accountId, isDarkModeEnabled);
  }

  updateUserRegionSettings(
    userId: number,
    accountId: string | null,
    regionId: number
  ): Observable<ServiceResultDTO<boolean>> {
    return this.userSettingsApiService.updateUserRegionSettings(userId, accountId, regionId);
  }

  loadUserInformation(): Observable<UserSettingsDTO | null> {
    this.loadUserSettings();
    return this.userSettings$;
  }

  private loadUserSettings() {
    const user = this.accountService.getCurrentUser();
    if (user != null) {
      const userId = Number(user.id);
      const accountId = user.accountId;
      this.userSettings$ = this._userSettingsData.pipe(
        switchMap(() => {
          return this.userSettingsApiService.getUserSettings(userId, accountId)
            .pipe(
              map((settings) => {
                if (settings.isSuccess) {
                  return settings.value
                } else {
                  return null;
                }
              }));
        }),
        share({
          connector: () => new ReplaySubject(1),
          resetOnComplete: () => timer(this.CACHE_TIMEOUT)
        }));
    }
  };
}
