import { IconDefinition } from '@fortawesome/free-brands-svg-icons';
import { Component, Input, OnInit } from '@angular/core';
import { faCirclePlay, faFilePdf, faLink } from '@fortawesome/pro-regular-svg-icons';
import { ResourceLinkDTO } from 'src/app/admin/resource-center/interfaces/resource-link.dto';

@Component({
  selector: 'app-resource-link-list',
  templateUrl: './resource-link-list.component.html',
  styleUrls: ['./resource-link-list.component.scss']
})
export class ResourceLinkListComponent implements OnInit {
  @Input()
  public resourceLinks?: ResourceLinkDTO[];

  public faCirclePlay = faCirclePlay;
  public faFilePdf = faFilePdf;
  public faLink = faLink;

  constructor() { }

  ngOnInit() {
  }

  public getIcon(type?: string) : IconDefinition{
    return type === "Link" ? this.faLink :
           type === "Video" ? this.faCirclePlay :
           this.faFilePdf;
  }
}
