import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { BaseAPIService } from "../../core/api/base.api.service";
import { ServiceResultDTO } from "../../core/interfaces/service-result/service-result.dto";
import { AuthenticationApiEndpoints } from "../constants/authentication-api.constants";
import { ForgotPasswordDTO } from "../interfaces/account/forgot-password.dto";
import { ResetPasswordDTO } from "../interfaces/account/reset-password.dto";
import { TokenDTO } from "../interfaces/auth-token/token.dto";
import { LoginDTO } from "../interfaces/login/login.dto";
import { WPUserAccountDTO } from "../interfaces/user/user-account.dto";

@Injectable({
  providedIn: "root"
})
export class AccountApiService extends BaseAPIService {
  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string,
    http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  public login(login: LoginDTO) {
    return this.http.post<ServiceResultDTO<WPUserAccountDTO>>(
      this.constructRequestUrl(AuthenticationApiEndpoints.LoginAdministrator()), login
    );
  }

  public forgotPassword(forgotPasswordDto: ForgotPasswordDTO) {
    return this.http.post<ServiceResultDTO<void>>(
      this.constructRequestUrl(AuthenticationApiEndpoints.ForgotPassword()), forgotPasswordDto
    );
  }

  public resetPassword(resetPasswordDto: ResetPasswordDTO) {
    return this.http.post<ServiceResultDTO<void>>(
      this.constructRequestUrl(AuthenticationApiEndpoints.ResetPassword()), resetPasswordDto
    );
  }

  refreshToken(token: TokenDTO) {
    return this.http.post<ServiceResultDTO<TokenDTO>>(
      this.constructRequestUrl(AuthenticationApiEndpoints.RefreshToken()), token);
  }
}
