import { Component, Input, OnInit, TemplateRef, ViewContainerRef } from '@angular/core';
import { RegionSelectorService } from 'src/app/core/services/region-selector.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { faEyeSlash } from '@fortawesome/pro-solid-svg-icons';

@Component({
  selector: 'tool-hidden-warning',
  templateUrl: './tool-hidden-warning.component.html',
  styleUrls: ['./tool-hidden-warning.component.scss']
})
export class ToolHiddenWarningComponent implements OnInit {
  faEyeSlash = faEyeSlash;

  @Input("toolId")
  public toolId!: number;

  public isContentHidden: boolean = false;
  public regionName: string | undefined = '';

  constructor(
    private regionSelectorService: RegionSelectorService,
    private sharedService: SharedService
  ) { }

  ngOnInit(): void {
    this.loadRegionName();
    this.loadIsContentHidden();
  }

  loadRegionName() {
    this.regionName = this.regionSelectorService.getCurrentRegion()?.name;
  }

  loadIsContentHidden() {
    this.sharedService.getToolIsContentHidden(this.toolId).subscribe((data) => {
      this.isContentHidden = data.isSuccess && data.value;
    });
  }
}
