import { Component, OnInit } from '@angular/core';
import { DefaultButtonComponent } from '../default-button/default-button.component';
import { faPenToSquare } from '@fortawesome/pro-regular-svg-icons';

@Component({
  selector: 'edit-button',
  templateUrl: '../default-button/default-button.component.html'
})
export class EditButtonComponent extends DefaultButtonComponent implements OnInit {
  constructor() {
    super();

    this.buttonIcon = faPenToSquare;
    if (this.text == '') this.text = 'Edit';
  }

  ngOnInit(): void {
  }
}
