import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { BaseAPIService } from '../../core/api/base.api.service';
import { ServiceResultDTO } from '../../core/interfaces/service-result/service-result.dto';
import { CoreApiEndpoints } from '../constants/core-api.constants';
import { FavoriteType } from '../constants/favorite-type';
import { UserFavoriteDTO } from '../interfaces/user-favorite.dto';
import { UserSettingsDTO } from '../interfaces/user-settings.dto';
import { UserResourceCenterDTO } from '../interfaces/user-resource-center.dto';

@Injectable({
  providedIn: 'root'
})
export class UserSettingsApiService extends BaseAPIService {

  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string, http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  getUserSettings(userId: number, accountId: string | null | undefined) {
    let params = new HttpParams()
      .set('accountId', accountId ?? '');

    return this.http.get<ServiceResultDTO<UserSettingsDTO>>(
      this.constructRequestUrl(CoreApiEndpoints.GetUserSettings(userId)), { params }
    );
  }

  toggleUserFavorite(
    userId: number,
    accountId: string | null,
    toolId: number,
    favoriteType: FavoriteType
  ) {
    let params = new HttpParams()
      .set('accountId', accountId ?? '');

    const body = {
      toolId: toolId,
      favoriteType: favoriteType
    }

    return this.http.put<ServiceResultDTO<UserSettingsDTO>>(
      this.constructRequestUrl(CoreApiEndpoints.ToggleUserFavorite(userId)), body, { params }
    );
  }

  toggleUserResourceCenterFavorite(
    userId: number,
    toolId: number
  ) {
      const body = {
        toolId: toolId
      }

      return this.http.put<ServiceResultDTO<UserSettingsDTO>>(
        this.constructRequestUrl(CoreApiEndpoints.ToggleUserResourceCenterFavorite(userId)), body
      );
  }

  updateUserFavoriteOrder(
    userId: number,
    accountId: string | null,
    favorite: UserFavoriteDTO
  ) {
    let params = new HttpParams()
      .set('accountId', accountId ?? '');

    return this.http.put<ServiceResultDTO<UserSettingsDTO>>(
      this.constructRequestUrl(CoreApiEndpoints.UpdateUserFavoriteOrder(userId)), favorite, { params }
    );
  }

  updateUserResourceCenterOrder(
    userId: number,
    accountId: string | null,
    tool: UserResourceCenterDTO
  ) {
    let params = new HttpParams()
      .set('accountId', accountId ?? '');

    return this.http.put<ServiceResultDTO<UserSettingsDTO>>(
      this.constructRequestUrl(CoreApiEndpoints.UpdateUserResourceCenterOrder(userId)), tool, { params }
    );
  }

  updateUserMenuThemeSettings(
    userId: number,
    accountId: string | null,
    isDarkModeEnabled: boolean
  ) {
    let params = new HttpParams()
      .set('accountId', accountId ?? '');

    const body = {
      isDarkModeEnabled: isDarkModeEnabled
    }

    return this.http.put<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(CoreApiEndpoints.UpdateUserMenuThemeSettings(userId)), body, { params }
    );
  }

  updateUserRegionSettings(
    userId: number,
    accountId: string | null,
    regionId: number
  ) {
    let params = new HttpParams()
      .set('accountId', accountId ?? '');

    const body = {
      regionId: regionId
    }

    return this.http.put<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(CoreApiEndpoints.UpdateUserRegionSettings(userId)), body, { params }
    );
  }
}
