import { Injectable } from '@angular/core';
import { GaitUserDTO } from '../interfaces/gait/gait-user.dto';
import { GaitApiService } from './gait.api.service';

@Injectable()
export class GaitService {

  constructor(private gaitApiService: GaitApiService) { }

  public authenticateGaitUser(contextId: string) {
    let gaitUserDTO = {
      contextId: contextId
    } as GaitUserDTO;

    return this.gaitApiService.authenticateGaitUser(gaitUserDTO);
  }

  public checkContextId(contextId: string) {
    return this.gaitApiService.checkContext(contextId);
  }

  public deleteContextId(contextId: string) {
    return this.gaitApiService.deleteContext(contextId);
  }

  public refreshContextId(contextId: string) {
    return this.gaitApiService.refrechContext(contextId);
  }
}
