let authenticationEndpoints : AuthenticationApiEndPointsType = {
  /* Gait */
  AuthenticateGaitUser: () => '/gait/authenticate',

  /* Account */
  LoginAdministrator: () => '/account/login',
  ForgotPassword: () => '/account/forgot-password',
  RefreshToken: () => '/account/refresh-token',
  ResetPassword: () => '/account/reset-password',
}

interface AuthenticationApiEndPointsType {
  /* Gait */
  readonly AuthenticateGaitUser: Function;

  /* Account */
  readonly LoginAdministrator: Function;
  readonly ForgotPassword: Function;
  readonly RefreshToken: Function;
  readonly ResetPassword: Function;
}

export const AuthenticationApiEndpoints: AuthenticationApiEndPointsType = authenticationEndpoints;
