import { AfterViewChecked, ChangeDetectorRef, Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { AccountService } from '../../../authentication/services/account.service';
import { faMemoCircleInfo, faChevronsLeft, faChevronsRight, faMoon, faSunBright } from '@fortawesome/pro-regular-svg-icons';
import { faChevronLeft, faChevronRight } from '@fortawesome/pro-light-svg-icons';
import { ResultHandlerService } from '../../services/result.handler.service';
import { SharedService } from '../../../shared/services/shared.service';
import { faScrewdriverWrench } from '@fortawesome/pro-solid-svg-icons';
import { WPUserAccountDTO } from '../../../authentication/interfaces/user/user-account.dto';
import { ApplicationUrls } from '../../../shared/constants/application-urls';
import { RegionSelectorService } from 'src/app/core/services/region-selector.service';
import { ToolDTO } from 'src/app/shared/interfaces/tools/tool.dto';
import { ToolsList } from 'src/app/shared/constants/tools-list.enum';
import { RoleUtils } from '../../utils/role.utils';
import { MenuLinkDTO } from '../../interfaces/menu-link.dto';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';
import { UserSettingsDTO } from '../../interfaces/user-settings.dto';
import { FavoriteType } from '../../constants/favorite-type';
import { Theme, ThemeService } from '../../services/theme.service';
import { MainMenuService } from '../../services/main-menu.service';
import { UserSettingsService } from '../../services/user-settings.service';
import { ServiceResultDTO } from '../../interfaces/service-result/service-result.dto';
import { UserFavoriteDTO } from '../../interfaces/user-favorite.dto';
import { MediaQueryService } from 'src/app/shared/services/media-query.service';

@Component({
  selector: 'main-menu',
  templateUrl: './main-menu.component.html',
  styleUrls: ['./main-menu.component.scss']
})
export class MainMenuComponent implements OnInit, AfterViewChecked {
  faMemoCircleInfo = faMemoCircleInfo;
  faChevronLeft = faChevronLeft;
  faChevronRight = faChevronRight;
  faChevronsLeft = faChevronsLeft;
  faChevronsRight = faChevronsRight;
  faScrewdriverWrench = faScrewdriverWrench;
  faSunBright = faSunBright;
  faMoon = faMoon;

  resourceCenterUrl = ApplicationUrls.ResourceCenter.path;

  public userSettings: UserSettingsDTO | null = null;
  private user: WPUserAccountDTO | null = null;
  private tools: ToolDTO[] | null = null;

  public favoriteLinks!: MenuLinkDTO[];
  public adminLinks!: MenuLinkDTO[];
  public otherLinks!: MenuLinkDTO[];
  private routerSubscription!: Subscription;

  public viewAll: boolean = false;
  public hasUserTheme: boolean = false;

  public theme$: Observable<[Theme, Theme]> = this.themeService.theme$;
  public isDark$: Observable<boolean> = this.themeService.isDark$;
  public isCollapsed: Observable<boolean> = this.mainMenuService.collapsed$;
  public isMobile$: Observable<boolean> = this.mediaQueryService.isMobile$;

  @Output()
  public toggleClick = new EventEmitter();

  constructor(
    private mediaQueryService: MediaQueryService,
    private accountService: AccountService,
    private sharedService: SharedService,
    private userSettingsService: UserSettingsService,
    private resultHandler: ResultHandlerService,
    private regionSelectorService: RegionSelectorService,
    private themeService: ThemeService,
    private mainMenuService: MainMenuService,
    private cd: ChangeDetectorRef
  ) {
    this.initialize();
    this.loadUserSettings();
  }

  ngOnInit(): void {
    this.subscribeRegionChange();
    this.loadTools();
  }

  /* RTE problem: Expression has changed after it was checked
   * http://www.syncfusion.com/forums/132152/rte-problem-expression-has-changed-after-it-was-checked?reply=ls7keM */
  ngAfterViewChecked() {
    this.cd.detectChanges();
  }

  ngOnDestroy(): void {
    this.routerSubscription?.unsubscribe();
  }

  initialize() {
    this.user = this.accountService.getCurrentUser();
  }

  loadUserSettings() {
    this.userSettingsService.userSettings$?.subscribe((data) => {
      if (data != null) {
        this.userSettings = data;
        this.hasUserTheme = true;
        this.themeService.setTheme(this.userSettings.isDarkModeEnabled ? 'warner-dark' : 'warner-light');
      }
    });
  }

  loadTools() {
    this.sharedService.getTools(undefined, undefined, undefined, true).subscribe((data) => {
      if (data.isSuccess) {
        this.tools = data.value;
        this.setMenuLinks();
      } else {
        this.resultHandler.handle(data);
      }
    });
  }

  onToggleClick = () => this.mainMenuService.toggleCollapsed();

  onLinkClick = () => this.mainMenuService.setCollapsed(true);

  setMenuLinks() {
    if (this.tools != null) {
      const currentRegion = this.regionSelectorService.regionSource.getValue()!;

      this.adminLinks = this.getAdminLinks().filter(l => {
        return this.user?.roles?.some(r => l.roles?.includes(r)) &&
          (l.regionsIds == undefined || l.regionsIds?.includes(currentRegion?.id));
      });

      this.otherLinks = this.getOtherLinks().filter(l => {
        const enabledToUserType = l.userTypes != null && l.userTypes.includes(this.user?.userType!);
        if (!enabledToUserType) return false;

        if (l.managedByRegion) {
          if (l.regionsIds != null && l.regionsIds?.includes(currentRegion?.id!)) {
            if (l.regionsContentHidden != null && l.regionsContentHidden.includes(currentRegion.id!)) {
              return this.user?.roles?.some(ur => l.roles?.includes(ur));
            }

            return true;
          }

          return false;
        }

        return true;
      });

      this.favoriteLinks = this.getFavoriteLinks();
    }
  }

  onChangeTheme(event: any) {
    if (this.user != null) {
      const userId = Number(this.user!.id);
      const isDarkModeEnabled = event.checked;

      this.themeService.setTheme(isDarkModeEnabled ? 'warner-dark' : 'warner-light')
      this.userSettingsService.updateUserMenuThemeSettings(userId, this.user?.accountId, isDarkModeEnabled).subscribe((data) => {
        this.resultHandler.handle(data);
        this.userSettings!.isDarkModeEnabled = isDarkModeEnabled;
      });
    }
  }

  subscribeRegionChange() {
    this.regionSelectorService.region$.subscribe((r) => {
      if (r != null) this.setMenuLinks();
    });
  }

  getFavoriteLinks(): MenuLinkDTO[] {
    const links: MenuLinkDTO[] = [];
    if (this.userSettings?.favorites) {
      this.userSettings.favorites.forEach(uf => {
        if (uf.favoriteType.toString() === FavoriteType[FavoriteType.AdminTool]) {
          this.adminLinks.filter(l => {
            return l.toolId == uf.toolId
          }).forEach(l => {
            links.push(l);
          });
        } else if (uf.favoriteType.toString() == FavoriteType[FavoriteType.OtherTool]) {
          this.otherLinks.filter(l => {
            return l.toolId == uf.toolId
          }).forEach(l => {
            links.push(l);
          });
        }
      });
    }

    return links;
  }

  getAdminLinks(): MenuLinkDTO[] {
    const links: MenuLinkDTO[] = [];
    const currentRegion = this.regionSelectorService.regionSource.getValue()?.abbreviation;
    if (currentRegion != '' && currentRegion != undefined) {
      links.push({
        toolId: ToolsList.GlobalToolSuite,
        text: ApplicationUrls.GlobalToolSuite.text,
        path: ApplicationUrls.GlobalToolSuite.path,
        icon: faScrewdriverWrench,
        roles: RoleUtils.getToolRoles(ToolsList.GlobalToolSuite),
        type: FavoriteType.AdminTool
      });
      links.push({
        toolId: ToolsList.Carrier411,
        text: ApplicationUrls.Carrier411Admin.text,
        path: ApplicationUrls.Carrier411Admin.path,
        iconPath: this.tools?.filter(t => t.id == ToolsList.Carrier411)[0].primaryLogoFilePath,
        regionsIds: this.tools?.filter(t => t.id == ToolsList.Carrier411)[0].regionsIds,
        roles: RoleUtils.getToolRoles(ToolsList.Carrier411, currentRegion),
        type: FavoriteType.AdminTool
      });
      links.push({
        toolId: ToolsList.LargeGroupRFP,
        text: ApplicationUrls.LargeGroupRFPAdmin.text,
        path: ApplicationUrls.LargeGroupRFPAdmin.path,
        iconPath: this.tools?.filter(t => t.id == ToolsList.LargeGroupRFP)[0].primaryLogoFilePath,
        roles: RoleUtils.getToolRoles(ToolsList.LargeGroupRFP, currentRegion),
        type: FavoriteType.AdminTool
      });
      links.push({
        toolId: ToolsList.DocumentToolkit,
        text: ApplicationUrls.DocumentToolkitAdmin.text,
        path: ApplicationUrls.DocumentToolkitAdmin.path,
        iconPath: this.tools?.filter(t => t.id == ToolsList.DocumentToolkit)[0].primaryLogoFilePath,
        roles: RoleUtils.getToolRoles(ToolsList.DocumentToolkit, currentRegion),
        type: FavoriteType.AdminTool
      });
      links.push({
        toolId: ToolsList.SecureDirectUpload,
        text: ApplicationUrls.SecureDirectUploadAdmin.text,
        path: ApplicationUrls.SecureDirectUploadAdmin.path,
        iconPath: this.tools?.filter(t => t.id == ToolsList.SecureDirectUpload)[0].primaryLogoFilePath,
        roles: RoleUtils.getToolRoles(ToolsList.SecureDirectUpload, currentRegion),
        type: FavoriteType.AdminTool
      });
    }

    return links;
  }

  getOtherLinks(): MenuLinkDTO[] {
    const links: MenuLinkDTO[] = [];
    if (this.tools != null) {
      const currentRegion = this.regionSelectorService.regionSource.getValue()?.abbreviation;
      if (currentRegion != '' && currentRegion != undefined) {
        this.tools.filter(t => !t.isAdminTool).map(t => {
          return {
            toolId: t.id,
            text: t.name,
            path: UrlUtils.getToolUrl(t.id, t.url) ?? '',
            iconPath: t.primaryLogoFilePath,
            userTypes: t.permissionGroupsIds,
            regionsIds: t.regionsIds,
            managedByRegion: t.isExternal || t.isManagedByStateAndCarrier,
            type: FavoriteType.OtherTool,
            regionsContentHidden: t.toolsManagement.filter(tm => tm.isContentHidden).map(tm => tm.region.id),
            roles: RoleUtils.getToolRoles(t.id, currentRegion)
          }
        }).forEach(l => links.push(l));
      }
    }

    return links;
  }

  otherLinksTitle = () => this.adminLinks != null && this.adminLinks.length > 0 ? 'Other' : '';

  onSortFavorite(link: MenuLinkDTO) {
    if (this.user != null) {
      const userId = Number(this.user!.id);
      const favorite: UserFavoriteDTO = {
        favoriteType: link.type!,
        order: link.order!,
        toolId: link.toolId!
      };
      this.userSettingsService.updateUserFavoriteOrder(userId, this.user?.accountId, favorite).subscribe((data) => {
        this.handleFavoriteUpdate(data);
      });
    }
  }

  toggleFavorite(link?: MenuLinkDTO) {
    if (link != null && this.user != null) {
      const userId = Number(this.user!.id);
      this.userSettingsService.toggleUserFavorite(userId, this.user.accountId, link.toolId!, link.type!).subscribe((data) => {
        this.handleFavoriteUpdate(data);
      });
    }
  }

  handleFavoriteUpdate(data: ServiceResultDTO<UserSettingsDTO>) {
    this.resultHandler.handle(data).then((success) => {
      if (success) {
        this.userSettings = data.value;
        this.favoriteLinks = this.getFavoriteLinks();
      }
    });
  }
}
