import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { BreadcrumbItemModel } from '@syncfusion/ej2-angular-navigations';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { isValidUrl } from 'src/app/shared/utilities/shared';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';

@Component({
  selector: 'custom-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  styleUrls: ['./breadcrumb.component.scss']
})
export class BreadcrumbComponent implements OnInit, OnDestroy {
  replaceParamFromUrl = UrlUtils.replaceParamFromUrl;
  applicationUrls = ApplicationUrls;
  isValidUrl = isValidUrl;

  breadcrumbItems: BreadcrumbItemModel[] = [];
  private routerSubscription!: Subscription;

  constructor(
    private router: Router
  ) {
    this.initialize();
  }

  ngOnInit(): void {
    this.setupBreadcrumb();
  }

  ngOnDestroy(): void {
    this.routerSubscription.unsubscribe();
  }

  private initialize() {
    this.routerSubscription = this.router.events.pipe(
      filter(event => event instanceof NavigationEnd)
    ).subscribe((res) => {
      this.setupBreadcrumb();
    });
  }

  public setupBreadcrumbLevel(urls: any) {
    for (var key of Object.keys(urls)) {
      if (typeof urls[key] === 'object') {
        this.setupBreadcrumbLevel(urls[key]);
      } else if (key == 'path' && urls.path != null) {
        this.checkPathAndAdd(urls);
      }
    }
  }

  public setupBreadcrumb() {
    this.breadcrumbItems = [];
    this.setupBreadcrumbLevel(ApplicationUrls);
  }

  redirectTo(url: string | undefined) {
    if (isValidUrl(url)) this.router.navigate([url]);
  }

  checkPathAndAdd(menu: any) {
    if (menu.text == undefined || menu.text == '') return;

    const path = menu.path.includes('{') ? this.replaceParamFromUrl(this.router.url, menu.path) : menu.path;    
    const possiblePaths = this.getPossiblePaths()

    if (possiblePaths.includes(path)) {
      this.breadcrumbItems.push({
        text: menu.text,
        url: menu.path
      });
    }
  }

  private getPossiblePaths(): string[] {
    const urlsParts = this.router.url.split(/[/]/g).filter(u => u != '');
    return urlsParts.map((element, i) => {
      let possiblePath = '';
      for (var j = 0; j <= i; j++) possiblePath = possiblePath.concat(`/${urlsParts[j]}`);

      return possiblePath;
    });
  }
}
