import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { CustomValidators } from '../utilities/custom-validators';

@Injectable({
  providedIn: 'root'
})
export class InputValidatorService {
  public areDifferentValues(form: FormGroup, field1: string, field2: string) {
    return (form.controls[field1].value != "" && form.controls[field2].value != "") &&
      form.controls[field1].value != form.controls[field2].value;
  }

  public isInvalidRequired(form: FormGroup, field: string) {
    return form.controls[field].hasError('required') && (form.controls[field].dirty || form.controls[field].touched);
  }

  public isInvalidEmail(form: FormGroup, field: string) {
    return form.controls[field].hasError('email') && (form.controls[field].dirty || form.controls[field].touched);
  }

  public isInvalidPhoneNumber(form: FormGroup, field: string) {
    let regex: RegExp = /\(([0-9]{3}|0{1}((x|[0-9]){2}[0-9]{2}))\)\s*[0-9]{3,4}[- ]*[0-9]{4}/;
    return (form.controls[field].dirty || form.controls[field].touched) && !regex.test(form.value.phoneNumber);
  }

  public isInvalidUrl(form: FormGroup, field: string) {
    const value = form.controls[field].value;
    return value != null && value != '' && !CustomValidators.urlRegex.test(value);
  }

  public isValidMultipleEmails(form: FormGroup, field: string) {
    const control = form.controls[field];
    var validation = CustomValidators.multipleEmailsValidator(control);

    return validation == null || validation["validEmails"];
  }
}
