import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { lastValueFrom } from 'rxjs';
import { AccountService } from '../services/account.service';
import { ToolService } from 'src/app/admin/tools-management/services/tool.service';
import { ToastService } from 'src/app/shared/services/toast.service';

@Injectable({
  providedIn: 'root'
})
export class UserTypeGuard implements CanActivate {
  constructor(
    private router: Router,
    private accountService: AccountService,
    private toolService: ToolService,
    private toastService: ToastService
  ) { }

  async canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {    
    var toolId = route.data["toolId"];
    if (!(typeof toolId === 'number') || toolId == null || toolId == 0) {
      return true;
    }

    const user = this.accountService.getCurrentUser();
    if (user == null) return false;

    const response = await lastValueFrom(this.toolService.getToolPermissionGroups(Number(toolId)));
    const userTypeAllowed = response.isSuccess && response.value.includes(user.userType);
    if (!userTypeAllowed) {
      this.router.navigate(['/']);
      this.toastService.show({ content: 'Access denied!', icon: 'error' });
    }

    return userTypeAllowed;
  }
}
