import { AfterContentChecked, Component, OnInit, ViewChild } from '@angular/core';
import { SidebarComponent } from '@syncfusion/ej2-angular-navigations';
import { MainMenuService } from '../../services/main-menu.service';
import { NavbarComponent } from '../navbar/navbar.component';
import { faBars } from '@fortawesome/pro-regular-svg-icons';
import { MediaQueryService } from 'src/app/shared/services/media-query.service';
import { Observable } from 'rxjs';
import { ThemeService } from '../../services/theme.service';

@Component({
  selector: 'app-app-container',
  templateUrl: './app-container.component.html',
  styleUrls: ['./app-container.component.scss']
})
export class AppContainerComponent implements AfterContentChecked, OnInit {

  navMenuVisible: boolean = false;
  public isOpen: boolean = true;

  @ViewChild("navMenu")
  private navbar!: NavbarComponent;
  @ViewChild('dockBar')
  dockBar!: SidebarComponent;
  faBars = faBars;

  public isMobile$: Observable<boolean> = this.mediaQueryService.isMobile$;
  public isDark$: Observable<boolean> = this.themeService.isDark$;

  constructor(
    private mediaQueryService: MediaQueryService,
    private themeService: ThemeService,
    private mainMenuService: MainMenuService
  ) {
  }

  ngOnInit() {
    this.mainMenuService.collapsed$.subscribe(collapsed => {
      this.isOpen = !collapsed;
      if (this.dockBar != null) this.dockBar.isOpen = !collapsed;
    });
  }

  ngAfterContentChecked(): void {
    this.navMenuVisible = this.navbar != null && this.navbar.navbar != null;
  }

  open(event: any) {
    this.mainMenuService.setCollapsed(false);
  }

  close(event: any) {
    this.mainMenuService.setCollapsed(true);
  }
}
