import { ToolbarSettingsModel } from "@syncfusion/ej2-angular-richtexteditor";
import { ToolsList } from "../constants/tools-list.enum";

export function tableCompareFunction(a: number | string | Date | undefined, b: number | string | Date | undefined, isAsc: boolean) {
  if (a && b)
    return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
  else if (a)
    return -1;
  else if (b)
    return 1;
  else
    return 1;
}

/*
 * The UTC DateTime object returned form the server should be formatted as an ISO string
 * so we can convert it to local time.
 * Example: "2021-11-01T20:15:04.0540837"
 * @param dateString
 */
export const DateDisplayFunctions = {
  createDateTimeDisplayValue,
  createTimeDisplayValue,
  createDateDisplayValue,
  createDateTimeLocalDisplayValueFromUTC
}

function createDateTimeDisplayValue(dateString: string) {
  if (!dateString) { throw "Provide a date"; }

  if (dateString[dateString.length - 1] !== "Z") {
    dateString += "Z";
  }

  var date = new Date(dateString);
  return date.toLocaleString();
}

function createTimeDisplayValue(dateString: string) {
  if (!dateString) { throw "Provide a date"; }

  if (dateString[dateString.length - 1] !== "Z") {
    dateString += "Z";
  }

  var date = new Date(dateString);
  return date.toLocaleTimeString();
}

function createDateDisplayValue(dateString: string) {
  if (!dateString) { throw "Provide a date"; }

  if (dateString[dateString.length - 1] !== "Z") {
    dateString += "Z";
  }
  var date = new Date(dateString);
  return date.toLocaleDateString();
}

function createDateTimeLocalDisplayValueFromUTC(date: Date): string {
  if (!date) return '';

  date = new Date(date);
  const utcTime = Date.UTC(
    date.getFullYear(),
    date.getMonth(),
    date.getDate(),
    date.getHours(),
    date.getMinutes(),
    date.getSeconds(),
    date.getMilliseconds()
  );
  date = new Date(utcTime);

  return date.toLocaleString('en-US', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
    timeZoneName: 'short'
  }).replace(',', '');
}

export function toFormData<T>(formValue: T) {
  const formData = new FormData();

  for (const key of Object.keys(formValue)) {
    const value = (formValue as any)[key];
    if (Array.isArray(value)) {
      for (var i = 0; i < value.length; i++) {
        formData.append(`${key}[${i}]`, value[i]);
      }
    }
    else {
      formData.append(key, value);
    }
  }

  return formData;
}

export function formatAdminToolName(tool: any): string {
  return tool.id == ToolsList.GlobalToolSuite ? tool.name : tool.name + ' Admin';
}

export function isGlobalToolSuite(toolId: number): boolean {
  return toolId == ToolsList.GlobalToolSuite;
}

export function isValidUrl(url: string | undefined) {
  return url != undefined && !url?.includes('{') && !url?.includes('NaN');
}

export function getFilePathEncoded(fullFilename: string) {
  const fileName = fullFilename.split('/').pop();
  if (fileName != undefined) {
    const filePath = fullFilename.replace(fileName, '');
    return `${filePath}${encodeURIComponent(fileName)}`;
  }

  return fullFilename;
}

export function getDefaultRichTextSettings(): ToolbarSettingsModel {
  return { items: ['Bold', 'Italic', 'Underline', 'Formats', '-', 'Alignments', 'OrderedList', 'UnorderedList', 'CreateLink'] };
}

export function getDefaultRichTextInlineMode(): object {
  return { enable: true, onSelection: true };
}

export function getDefaultRichTextFormat(): object {
  return { width: 'auto' };
}

export function getMaxZIndex() {
  return Math.max(
    ...Array.from(document.querySelectorAll('body *'), el =>
      parseFloat(window.getComputedStyle(el).zIndex),
    ).filter(zIndex => !Number.isNaN(zIndex)),
    0,
  );
}
