import { AfterViewInit, Component, ElementRef, Input, ViewChild } from '@angular/core';
import { faCircleQuestion } from '@fortawesome/pro-regular-svg-icons';

@Component({
  selector: 'helper-tooltip',
  templateUrl: './helper-tooltip.component.html',
  styleUrls: ['./helper-tooltip.component.scss']
})
export class HelperTooltipComponent implements AfterViewInit {
  faCircleQuestion = faCircleQuestion;

  @Input()
  public title: string = 'Recommendation';
  @Input()
  public position: 'left' | 'right' | 'center' | 'justify' | 'initial' | 'inherit' = 'right';
  @Input()
  public text: string = '';

  @ViewChild("box")
  box!: ElementRef;

  constructor() { }

  ngAfterViewInit(): void {
    this.box!.nativeElement.style.textAlign = this.position;
  }
}
