import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { ToolService } from 'src/app/admin/tools-management/services/tool.service';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { getDefaultRichTextFormat, getDefaultRichTextInlineMode, getDefaultRichTextSettings } from '../../utilities/shared';

@Component({
  selector: 'announcement-message-edit',
  templateUrl: './announcement-message-edit.component.html',
  styleUrls: ['./announcement-message-edit.component.scss']
})
export class AnnouncementMessageEditComponent implements OnInit, OnChanges {
  @Input("description")
  public description: string = "";
  @Input("toolId")
  public toolId: number = 0;
  @Input('announcementMessage')
  public announcementMessage: string | undefined = "";
  public newMessage: string = "";

  public inlineMode: object = getDefaultRichTextInlineMode();
  public toolbarSettings: object = getDefaultRichTextSettings();
  public format: object = getDefaultRichTextFormat();

  constructor(
    private toolService: ToolService,
    private resultHandlerService: ResultHandlerService
  ) { }

  ngOnInit() {
  }

  ngOnChanges(): void {
    this.newMessage = this.announcementMessage!;
  }

  onUndo() {
    this.newMessage = this.announcementMessage!;
  }

  onSave() {
    this.toolService.updateAnnouncementMessage(this.toolId, this.newMessage).subscribe((data) => {
      this.resultHandlerService.handle(data).then((success) => {
        if (success) this.announcementMessage = data.value;
      });
    });
  }
}
