import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { BaseAPIService } from '../../core/api/base.api.service';
import { SharedApiEndpoints } from '../constants/shared-api.constants';
import { ServiceResultDTO } from '../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../interfaces/tools/tool.dto';
import { ResourceCenterDTO } from '../interfaces/resource-center/resource-center.dto';

@Injectable({
  providedIn: 'root'
})
export class SharedApiService extends BaseAPIService {

  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string, http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  getTools(hasRoles?: boolean, isExternal?: boolean, isAdminTool?: boolean, isActive?: boolean) {
    let params = new HttpParams()
      .set('hasRoles', hasRoles ?? '')
      .set('isExternal', isExternal ?? '')
      .set('isAdminTool', isAdminTool ?? '')
      .set('isActive', isActive ?? '');

    return this.http.get<ServiceResultDTO<ToolDTO[]>>(
      this.constructRequestUrl(SharedApiEndpoints.GetTools()), { params }
    );
  }

  getTool(id: number) {
    return this.http.get<ServiceResultDTO<ToolDTO>>(
      this.constructRequestUrl(SharedApiEndpoints.GetTool(id))
    );
  }

  getToolIsContentHidden(id: number) {
    return this.http.get<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(SharedApiEndpoints.GetToolIsContentHidden(id))
    );
  }

  getResourceCenter() {
    return this.http.get<ServiceResultDTO<ResourceCenterDTO>>(
      this.constructRequestUrl(SharedApiEndpoints.GetResourceCenter())
    );
  }
}
