import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { ToolCreateDTO } from '../interfaces/tool-create.dto';
import { ToolDTO } from '../../../shared/interfaces/tools/tool.dto';
import { ToolManagementUpdateDTO } from '../../../shared/interfaces/tools/tool-management-update.dto';
import { ToolUpdateDTO } from '../interfaces/tool-update.dto';
import { ToolUpdatePermissionsDTO } from '../interfaces/tool-update-permissions.dto';
import { AdminApiEndpoints } from '../../constants/admin-api.constants';
import { CarrierDTO } from '../../carrier-management/interfaces/carrier.dto';
import { ToolMessagesDTO } from 'src/app/shared/interfaces/tools/tool-messages.dto';

@Injectable({
  providedIn: 'root'
})
export class ToolApiService extends BaseAPIService {

  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string, http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  public getManageableTools(isActive: boolean) {
    let params = new HttpParams()
      .set('isActive', isActive.toString())

    return this.http.get<ServiceResultDTO<ToolDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetManageableTools()), { params }
    );
  }

  public getToolPermissionGroups(id: number) {
    return this.http.get<ServiceResultDTO<number[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetToolPermissionGroups(id))
    );
  }

  public getToolActiveCarriers(id: number) {
    return this.http.get<ServiceResultDTO<CarrierDTO[]>>(
      this.constructRequestUrl(AdminApiEndpoints.GetToolActiveCarriers(id))
    );
  }

  public isToolActiveInCurrentRegion(toolId: number) {
    return this.http.get<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.IsToolActiveInCurrentRegion(toolId))
    );
  }

  public createTool(tool: ToolCreateDTO, primaryLogo?: File, fullSizeLogo?: File) {
    const formData = new FormData();
    formData.append('toolJsonString', JSON.stringify(tool));

    if (primaryLogo != null) formData.append("primaryLogo", primaryLogo);
    if (fullSizeLogo != null) formData.append("fullSizeLogo", fullSizeLogo);

    return this.http.post<ServiceResultDTO<ToolDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.CreateTool()), formData
    );
  }

  public validateUpdate(id: number, tool: ToolUpdateDTO, primaryLogo: File | undefined) {
    const formData = new FormData();
    formData.append('toolJsonString', JSON.stringify(tool));

    if (primaryLogo != null) formData.append("primaryLogo", primaryLogo);

    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateUpdateTool(id)), formData
    );
  }

  public updateTool(id: number, tool: ToolUpdateDTO, primaryLogo?: File, fullSizeLogo?: File) {
    const formData = new FormData();
    formData.append('toolJsonString', JSON.stringify(tool));

    if (primaryLogo != null) formData.append("primaryLogo", primaryLogo);
    if (fullSizeLogo != null) formData.append("fullSizeLogo", fullSizeLogo);

    return this.http.put<ServiceResultDTO<ToolDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateTool(id)), formData
    );
  }

  public validateUpdateGeneralInfo(id: number, tool: ToolUpdateDTO, primaryLogo: File | undefined) {
    const formData = new FormData();
    formData.append('toolJsonString', JSON.stringify(tool));

    if (primaryLogo != null) formData.append("primaryLogo", primaryLogo);

    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateUpdateToolGeneralInfo(id)), formData
    );
  }

  public updateGeneralInfo(id: number, tool: ToolUpdateDTO, primaryLogo?: File, fullSizeLogo?: File) {
    const formData = new FormData();
    formData.append('toolJsonString', JSON.stringify(tool));

    if (primaryLogo != null) formData.append("primaryLogo", primaryLogo);
    if (fullSizeLogo != null) formData.append("fullSizeLogo", fullSizeLogo);

    return this.http.put<ServiceResultDTO<ToolDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateToolGeneralInfo(id)), formData
    );
  }

  public validateUpdatePermissions(id: number, tool: ToolUpdatePermissionsDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateUpdateToolPermissions(id)), tool
    );
  }

  public updatePermissions(id: number, tool: ToolUpdatePermissionsDTO) {
    return this.http.put<ServiceResultDTO<ToolDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateToolPermissions(id)), tool
    );
  }

  public validateUpdateToolManagement(id: number, toolManagement: ToolManagementUpdateDTO[]) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateUpdateToolManagement(id)), toolManagement
    );
  }

  public updateToolManagement(id: number, toolManagement: ToolManagementUpdateDTO[]) {
    return this.http.put<ServiceResultDTO<ToolDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateToolManagement(id)), toolManagement
    );
  }

  public updateToolOrder(id: number, newOrder: number) {
    var tool = { order: newOrder };
    return this.http.put<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateToolOrder(id)), tool
    );
  }

  public validateDelete(tool: ToolDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateDeleteTool()), tool
    );
  }

  public delete(id: number) {
    return this.http.delete<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.DeleteTool(id))
    );
  }

  public validateRestore(tool: ToolDTO) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.ValidateRestoreTool()), tool
    );
  }

  public restoreTool(id: number) {
    return this.http.post<ServiceResultDTO<boolean>>(
      this.constructRequestUrl(AdminApiEndpoints.RestoreTool(id)), null
    );
  }

  public updateWelcomeMessage(id: number, welcomeMessage: string) {
    var tool = { welcomeMessage: welcomeMessage };
    return this.http.put<ServiceResultDTO<string>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateToolWelcomeMessage(id)), tool
    );
  }

  public updateFavoriteMessage(id: number, favoriteMessage: string) {
    var tool = { favoriteMessage: favoriteMessage };
    return this.http.put<ServiceResultDTO<string>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateToolFavoriteMessage(id)), tool
    );
  }

  public updateAnnouncementMessage(id: number, announcementMessage: string) {
    var tool = { announcementMessage: announcementMessage };
    return this.http.put<ServiceResultDTO<string>>(
      this.constructRequestUrl(AdminApiEndpoints.UpdateToolAnnouncementMessage(id)), tool
    );
  }

  public getToolMessages(toolId: number) {
    return this.http.get<ServiceResultDTO<ToolMessagesDTO>>(
      this.constructRequestUrl(AdminApiEndpoints.GetToolMessages(toolId))
    );
  }
}
