import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { RemovingEventArgs, SelectedEventArgs, UploaderComponent } from '@syncfusion/ej2-angular-inputs';
import { faArrowRightLong, faFilePdf, faFileVideo } from '@fortawesome/pro-regular-svg-icons';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { FileManagerService } from 'src/app/admin/file-management/services/file-manager.service';
import { getFilePathEncoded } from '../../utilities/shared';

@Component({
  selector: 'single-upload',
  templateUrl: './single-upload.component.html',
  styleUrls: ['./single-upload.component.scss']
})
export class SingleUploadComponent implements OnInit {
  getFilePathEncoded = getFilePathEncoded;

  faArrowRightLong = faArrowRightLong;
  faFilePdf = faFilePdf;
  faFileVideo = faFileVideo;

  public imageExtensions: string = '.svg, .png, .jpg, .jpeg';
  public pdfExtension: string = '.pdf';
  public videoExtension: string = '.mp4, .mov, .wmv, .avi, .avchd, .flv, .f4v, .swf, .mkv, .webm, .mpeg-2';

  @Input()
  public title: string = '';
  @Input()
  public autoUpload: boolean = false;
  @Input()
  public allowExtensions: string = this.imageExtensions;
  @Input()
  public tooltip: string = 'We recommend SVG format with a 1:1 aspect ratio.';
  @Input()
  public currentFilePath: string | undefined = '';
  @Input()
  public showRecommendation: boolean = true;
  @Input()
  public required: boolean = true;
  @Input()
  public maxFileSize: number = 30000000;

  @ViewChild("uploader")
  private uploader!: UploaderComponent;
  public previewSrc: SafeUrl = '';
  public previewFileName: string = '';

  public files!: any[];
  public currentFile: Blob | null | undefined = null;
  public currentFilePathPreview: string | undefined = '';

  constructor(
    private fileManagerService: FileManagerService,
    private domSanitizer: DomSanitizer
  ) { }

  ngOnInit(): void {
    this.currentFilePathPreview = this.currentFilePath;
  }

  uploaderCreated = () => this.loadCurrentFile();

  public getFile(): File | undefined {
    const filesUploaded = this.uploader.getFilesData();
    if (filesUploaded != null && filesUploaded.length > 0 && filesUploaded.every(f => f.statusCode != "0")) {
      const rawFile = filesUploaded[0].rawFile;
      if (rawFile == null || rawFile == '') {
        return this.currentFile as File;
      }

      return rawFile as File;
    }

    return undefined;
  }

  onSelect(args: SelectedEventArgs): void {
    const file = args.filesData[0];
    if (!this.allowExtensions.includes(file.type)) {
      args.cancel = true;
      return;
    }

    let reader: FileReader = new FileReader();
    reader.addEventListener('load', () => {
      this.previewSrc = this.domSanitizer.bypassSecurityTrustResourceUrl(reader.result as string);
    }, false);

    if (file) {
      reader.readAsDataURL(file.rawFile as File);
      this.previewFileName = (file.rawFile as File).name;
      this.currentFilePathPreview = this.currentFilePath;
    }
  }

  onFileRemove(args: RemovingEventArgs): void {
    if (this.previewSrc == '') {
      this.files = [];
      this.currentFilePathPreview = '';
    } else if (!this.required) {
      this.previewCurrentFile();
    }

    this.previewSrc = '';
  }

  reset(): void {
    this.previewSrc = '';
    this.uploader.clearAll();
    this.previewCurrentFile();
  }

  isImage(): boolean {
    return this.allowExtensions == this.imageExtensions;
  }
  isPdf(): boolean {
    return this.allowExtensions == this.pdfExtension;
  }
  isVideo(): boolean {
    return this.allowExtensions == this.videoExtension;
  }

  getCurrentFileName(withExtension: boolean = true): string {
    const fullName = this.currentFilePath?.split('/')?.pop() ?? '';
    if (withExtension) return fullName;

    return fullName.replace(`.${this.getCurrentFileType()}`, '');
  }

  getCurrentFileType = (): string => this.currentFilePath?.split('.')?.pop() ?? '';

  loadCurrentFile() {
    /* When the file is not required, it will allow the user to remove the current file. */
    if (!this.required
      && this.currentFilePath != undefined
      && this.currentFilePath != '') {
      this.fileManagerService.getFromFullPath(this.currentFilePath).subscribe((file) => {
        this.currentFile = new File([file], this.getCurrentFileName());
        this.previewCurrentFile();
      });
    }
  }

  previewCurrentFile() {
    if (this.currentFilePath != '' && this.currentFile != null) {
      this.files = [{
        name: this.getCurrentFileName(false),
        size: this.currentFile.size,
        type: this.getCurrentFileType()
      }];
    } else {
      this.files = [];
    }

    this.currentFilePathPreview = this.currentFilePath;
  }

  reloadCurrentFile(file?: File, filePath?: string) {
    this.currentFile = file as Blob;
    this.currentFilePath = filePath;
    this.reset();
  }
}
