import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../core/interfaces/service-result/service-result.dto';
import { ResourceCenterDTO } from '../interfaces/resource-center/resource-center.dto';
import { ToolDTO } from '../interfaces/tools/tool.dto';
import { SharedApiService } from './shared.api.service';

@Injectable({
  providedIn: 'root'
})
export class SharedService {
  constructor(
    private sharedApiService: SharedApiService
  ) {
  }

  getTools(hasRoles?: boolean, isExternal?: boolean, isAdminTool?: boolean, isActive?: boolean): Observable<ServiceResultDTO<ToolDTO[]>> {
    return this.sharedApiService.getTools(hasRoles, isExternal, isAdminTool, isActive);
  }

  getTool(id: number): Observable<ServiceResultDTO<ToolDTO>> {
    return this.sharedApiService.getTool(id);
  }

  getToolIsContentHidden(id: number) {
    return this.sharedApiService.getToolIsContentHidden(id);
  }

  getResourceCenter(): Observable<ServiceResultDTO<ResourceCenterDTO>> {
    return this.sharedApiService.getResourceCenter();
  }
}
