import { Injectable } from '@angular/core'
import { BehaviorSubject } from 'rxjs';
import { ApplicationUrls } from '../../shared/constants/application-urls';
import { NavElements } from '../constants/nav-elements.constants';
import { NavBarDTO } from '../interfaces/nav-bar.dto';
import { ToolDTO } from '../../shared/interfaces/tools/tool.dto';
import { SharedService } from '../../shared/services/shared.service';

@Injectable({
  providedIn: 'root'
})
export class NavService {
  // Observable navbar source
  private navbarSource = new BehaviorSubject<NavBarDTO | undefined>(undefined);
  // Observable navbar stream
  navbar$ = this.navbarSource.asObservable();
  // service command
  changeNav(navbar: NavBarDTO) {
    this.navbarSource.next(navbar);
  }

  // dynamic menu for the Role Management where it lists tools
  setRoleManagementMenu(sharedService: SharedService) {
    const hasArrayInHistoryState = history.state == null ||
      typeof history.state !== 'object' ||
      history.state.data == undefined ||
      history.state.data == null ||
      !(history.state.data instanceof Array);
    if (hasArrayInHistoryState) {
      const currentNavbar = this.navbarSource.getValue();
      const differentNavBar = currentNavbar == undefined || currentNavbar.title != ApplicationUrls.GlobalToolSuite.RoleManagement.text;
      if (differentNavBar) {
        sharedService.getTools(true, false, undefined, true).subscribe(t => {
          if (t.isSuccess) {
            const tools = t.value.sort((a, b) => a.id > b.id ? 1 : -1);
            const navbar = NavElements.GetRoleManagement(tools);
            this.changeNav(navbar);
          }
        });
      }
    } else {
      const navbar = NavElements.GetRoleManagement(history.state.data);
      this.changeNav(navbar);
    }
  }

  // dynamic menu based on the User Type
  setUserManagementNavMenu(userTypeName: string) {
    const menu = NavElements.GetUserManagement(userTypeName);
    this.changeNav(menu);
  }

  // dynamic menu based on the Tool
  setToolNavMenu(tool: ToolDTO) {
    const menu = NavElements.GetToolManagement(tool);
    this.changeNav(menu);
  }
}
