import { NgModule } from '@angular/core';
import { AccordionModule } from '@syncfusion/ej2-angular-navigations';
import { BreadcrumbModule } from '@syncfusion/ej2-angular-navigations';
import { ButtonModule } from '@syncfusion/ej2-angular-buttons';
import { CheckBoxModule } from '@syncfusion/ej2-angular-buttons';
import { ComboBoxModule, MultiSelectModule } from '@syncfusion/ej2-angular-dropdowns';
import { DateTimePickerModule } from '@syncfusion/ej2-angular-calendars';
import { DetailRowService, FilterService, GroupService, PageService, SearchService, SortService, ToolbarService as TbgService, RowDDService } from '@syncfusion/ej2-angular-grids';
import { DialogModule } from '@syncfusion/ej2-angular-popups';
import { DropDownButtonModule } from '@syncfusion/ej2-angular-splitbuttons';
import { DropDownListModule } from '@syncfusion/ej2-angular-dropdowns';
import { FileManagerModule, NavigationPaneService, ToolbarService as TbfmService, DetailsViewService } from '@syncfusion/ej2-angular-filemanager';
import { GridModule } from '@syncfusion/ej2-angular-grids';
import { ListBoxModule } from '@syncfusion/ej2-angular-dropdowns';
import { RadioButtonModule } from '@syncfusion/ej2-angular-buttons';
import { RichTextEditorAllModule } from '@syncfusion/ej2-angular-richtexteditor';
import { SidebarModule } from '@syncfusion/ej2-angular-navigations';
import { SplitButtonModule } from '@syncfusion/ej2-angular-splitbuttons';
import { SplitterModule } from '@syncfusion/ej2-angular-layouts';
import { SwitchModule } from '@syncfusion/ej2-angular-buttons';
import { MaskedTextBoxModule, TextBoxModule } from '@syncfusion/ej2-angular-inputs';
import { ToastModule } from '@syncfusion/ej2-angular-notifications';
import { TooltipModule } from '@syncfusion/ej2-angular-popups';
import { TreeViewModule } from '@syncfusion/ej2-angular-navigations';
import { UploaderModule } from '@syncfusion/ej2-angular-inputs';

const SyncfusionModules = [
  AccordionModule,
  BreadcrumbModule,
  ButtonModule,
  CheckBoxModule,
  ComboBoxModule,
  DateTimePickerModule,
  DialogModule,
  DropDownButtonModule,
  DropDownListModule,
  FileManagerModule,
  GridModule,
  ListBoxModule,
  MaskedTextBoxModule,
  MultiSelectModule,
  RadioButtonModule,
  RichTextEditorAllModule,
  SidebarModule,
  SplitButtonModule,
  SplitterModule,
  SwitchModule,
  ToastModule,
  TooltipModule,
  TextBoxModule,
  TreeViewModule,
  UploaderModule
]

@NgModule({
  imports: [SyncfusionModules],
  exports: [SyncfusionModules],
  providers: [
    FilterService,
    GroupService,
    DetailRowService,
    DetailsViewService,
    NavigationPaneService,
    PageService,
    RowDDService,
    SearchService,
    SortService,
    TbfmService,
    TbgService
  ]
})

export class SyncfusionModule { }
