import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { lastValueFrom } from 'rxjs';
import { Carrier411RolesService } from 'src/app/carrier411/shared/services/carrier411-roles.service';
import { ToolsList } from 'src/app/shared/constants/tools-list.enum';
import { ToastService } from 'src/app/shared/services/toast.service';
import { RegionSelectorService } from '../../../core/services/region-selector.service';
import { SharedService } from '../../../shared/services/shared.service';

@Injectable({
  providedIn: 'root'
})
export class Carrier411VisibleInRegionGuard implements CanActivate {
  constructor(
    private router: Router,
    private regionSelectorService: RegionSelectorService,
    private sharedService: SharedService,
    private carrier411RolesService: Carrier411RolesService,
    private toastService: ToastService
  ) { }

  async canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    if (this.carrier411RolesService.isAdmin()) {
      return true;
    }

    const region = this.regionSelectorService.getCurrentRegion();
    if (region == null) return false;

    const response = await lastValueFrom(this.sharedService.getToolIsContentHidden(Number(ToolsList.Carrier411)));
    const regionHidden = response.isSuccess && response.value;
    if (regionHidden) {
      this.router.navigate(['/']);
      this.toastService.show({ content: 'Access denied!', icon: 'error' });
    }

    return !regionHidden;
  }
}
