import { Component, Input, OnInit } from '@angular/core';
import { WorkflowStep } from 'src/app/carrier411/shared/constants/workflow-step.enum';
import { faCircle } from '@fortawesome/pro-solid-svg-icons';
import { WorkflowStepUtils } from '../../utilities/workflow-step-utils';

@Component({
  selector: 'workflow-step',
  templateUrl: './workflow-step.component.html',
  styleUrls: ['./workflow-step.component.scss']
})
export class WorkflowStepComponent implements OnInit {
  WorkflowStep = WorkflowStep;

  @Input("step")
  public workflowStep: WorkflowStep = WorkflowStep.Draft;
  @Input("showDescription")
  public showDescription: boolean = true;

  icon = faCircle;

  constructor() { }

  ngOnInit(): void {
  }

  getClass() {
    if (typeof this.workflowStep == 'number') {
      return WorkflowStep[this.workflowStep].toString().toLowerCase()
    }

    return (this.workflowStep + '').toString().toLowerCase();
  }

  getDescription(): string | null | undefined {
    return WorkflowStepUtils.getDescription(this.workflowStep);
  }
}
