import { AfterViewChecked, ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { faStar as fasStar } from '@fortawesome/pro-solid-svg-icons';
import { faStar as farStar } from '@fortawesome/pro-regular-svg-icons';
import { ListBoxComponent } from '@syncfusion/ej2-angular-dropdowns';
import { WPUserAccountDTO } from 'src/app/authentication/interfaces/user/user-account.dto';
import { AccountService } from 'src/app/authentication/services/account.service';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { UserResourceCenterDTO } from 'src/app/core/interfaces/user-resource-center.dto';
import { UserSettingsDTO } from 'src/app/core/interfaces/user-settings.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { UserSettingsService } from 'src/app/core/services/user-settings.service';
import { ResourceCenterDTO } from 'src/app/shared/interfaces/resource-center/resource-center.dto';
import { ToolDTO } from 'src/app/shared/interfaces/tools/tool.dto';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-resource-center',
  templateUrl: './resource-center.component.html',
  styleUrls: ['./resource-center.component.scss']
})
export class ResourceCenterComponent implements OnInit, AfterViewChecked {
  public userSettings: UserSettingsDTO | null = null;
  public descriptionPage!: string;
  public fasStar = fasStar;
  public farStar = farStar;
  public resourceCenter!: ResourceCenterDTO;
  public tools!: ToolDTO[];
  public toolsLeft!: ToolDTO[];
  public toolsRight!: ToolDTO[];
  private user: WPUserAccountDTO | null = null;

  @ViewChild("listObjLeft")
  public listLeft?: ListBoxComponent;

  @ViewChild("listObjRight")
  public listRight?: ListBoxComponent;

  constructor(
    route: ActivatedRoute,
    private accountService: AccountService,
    private resultHandler: ResultHandlerService,
    private sharedService: SharedService,
    private cd: ChangeDetectorRef,
    private userSettingsService: UserSettingsService
  ) {
    this.resourceCenter = (route.snapshot.data["resourceCenter"] as ServiceResultDTO<ResourceCenterDTO>).value;
    this.user = this.accountService.getCurrentUser();

    this.loadUserSettings();
  }

  handleTools(tools: ServiceResultDTO<ToolDTO[]>) {
    if (tools.isSuccess) {
      if (this.userSettings?.resourceCenterSettings != null && this.userSettings?.resourceCenterSettings.length){
        this.userSettings?.resourceCenterSettings.forEach(x => {
          let index = tools.value.findIndex(obj => obj.id == x.toolId);
          if(index != -1){
            tools.value[index].order = x.order;
            tools.value[index].isFavoritedByDefault = x.isFavorited!;
          }
        });
      }

      this.tools = tools.value.sort((a, b) => a.order > b.order ? 1 : -1);
      this.toolsLeft = this.tools.filter(obj => obj.order % 2 != 0);
      this.toolsRight = this.tools.filter(obj => obj.order % 2 == 0);
    } else {
      this.resultHandler.handle(tools);
    }
  }

  ngOnInit() {
    this.user = this.accountService.getCurrentUser();
    this.descriptionPage = this.resourceCenter?.description;
  }

  /* RTE problem: Expression has changed after it was checked
  * http://www.syncfusion.com/forums/132152/rte-problem-expression-has-changed-after-it-was-checked?reply=ls7keM */
  ngAfterViewChecked() {
    this.cd.detectChanges();
  }

  loadTools(): void {
    if (this.user?.id != null) {
      this.sharedService.getTools(undefined, undefined, false, true).subscribe((data) => {
        this.handleTools(data);
      }, (err) => this.resultHandler.handle(err.error));
    }
  }

  toggleFavorite(toolId: number) {
    this.userSettingsService.toggleUserResourceCenterFavorite(Number(this.user?.id), toolId).subscribe((data) => {
      if (data != null) {
        this.loadUserSettings();
      }
    });
  }

  loadUserSettings() {
    this.userSettingsService.loadUserInformation()?.subscribe((data) => {
      if (data != null) {
        this.userSettings = data;
        this.loadTools();
      }
    });
  }

  setNewOrder($event: any){
    const tool = $event.items[0] as ToolDTO;

    const newOrder = this.getNewOrder($event);

    let model: UserResourceCenterDTO = { toolId: tool.id, order: newOrder, isFavorited: undefined };

    this.userSettingsService.updateUserResourceCenterOrder(Number(this.user!.id), this.user!.accountId, model).subscribe((data) => {
      this.resultHandler.handle(data).then((success) => {
        if (success) {
          this.loadUserSettings();
        }
      });
    }, (err) => this.resultHandler.handle(err.error));
  }

  checkOrder($event: any){
    const newOrder = this.getNewOrder($event);

    if (newOrder > this.tools.length) {
      $event.cancel = true;
      return;
    }
  }

  getNewOrder($event: any): number {
    let listLeft = this.listLeft!.getDataList() as any[];

    const tool = $event.items[0] as ToolDTO;
    let newOrder = ($event.currentIndex + 1) * 2;

    if (listLeft.filter(x => x.id == tool.id).length){
      newOrder -= 1;
    }

    return newOrder;
  }
}
