import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { environment } from '../../../../environments/environment';
import { ResultHandlerService } from '../../../core/services/result.handler.service';
import { InputValidatorService } from '../../../shared/services/input-validator.service';
import { ForgotPasswordDTO } from '../../interfaces/account/forgot-password.dto';
import { AccountService } from '../../services/account.service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {

  public form!: FormGroup;
  public showMessage: boolean = false;
  public isSuccess: boolean = false;
  public userMessage!: string;

  constructor(private resultHandler: ResultHandlerService,
    public inputValidatorService: InputValidatorService,
    private fb: FormBuilder,
    private accountService: AccountService) { }

  ngOnInit(): void {
    this.initializeForm();
  }

  initializeForm() {
    this.form = this.fb.group({
      email: ['', [Validators.required, Validators.email]]
    })
  }

  public forgotPassword() {

    let formValues = this.form.value;
    let forgotPasswordDto: ForgotPasswordDTO = {
      email: formValues.email,
      clientUri: environment.client.baseUrl
    }

    this.accountService.forgotPassword(forgotPasswordDto).subscribe(
      (data) => { this.resultHandler.handle(data); },
      (err) => { this.resultHandler.handle(err.error); });
  }

}
