import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { UrlUtils } from 'src/app/shared/utilities/url-utils';
import { ApplicationUrls } from '../../../shared/constants/application-urls';
import { NavElements } from '../../constants/nav-elements.constants';
import { NavMenus } from '../../constants/nav-menus.enums';
import { NavBarDTO } from '../../interfaces/nav-bar.dto';
import { NavService } from '../../services/nav.service';

@Component({
  selector: 'navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit, OnDestroy {
  replaceParamFromUrl = UrlUtils.replaceParamFromUrl;
  public navbar: NavBarDTO | undefined = undefined;
  public state!: any;
  private routerSubscription!: Subscription;
  private navSubscription!: Subscription;

  constructor(
    private router: Router,
    private navService: NavService
  ) {
    this.initialize();
  }

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this.routerSubscription.unsubscribe();
    this.navSubscription.unsubscribe();
  }

  private initialize() {
    this.routerSubscription = this.router.events.pipe(
      filter(event => event instanceof NavigationEnd)
    ).subscribe((res) => {
      this.state = history.state;
      this.setCurrentNavBar();
      this.replaceLinksParams();
    });

    this.navSubscription = this.navService.navbar$.subscribe(item => {
      this.navbar = item;
      this.replaceLinksParams();
    });
  }

  setCurrentNavBar(): void {
    switch (this.getNavMenu()) {
      case NavMenus.GlobalToolSuite:
        this.navbar = NavElements.GetGlobalToolSuite();
        break;
      case NavMenus.ToolManagement:
      case NavMenus.UserManagement:
      case NavMenus.RoleManagement:
        // Being handled by the NavService.
        break;
      case NavMenus.Carrier411Admin:
        this.navbar = NavElements.GetCarrier411Admin();
        break;
      default:
        this.navbar = undefined;
        break;
    }
  }

  getNavMenu(): NavMenus {
    if (this.router.url.endsWith(ApplicationUrls.GlobalToolSuite.path) ||
      this.router.url.endsWith(ApplicationUrls.GlobalToolSuite.UserManagement.path) ||
      this.router.url.endsWith(ApplicationUrls.GlobalToolSuite.UserManagement.NewAdministrator.path) ||
      this.router.url.endsWith(ApplicationUrls.GlobalToolSuite.RoleManagement.path) ||
      this.router.url.includes(ApplicationUrls.GlobalToolSuite.StateManagement.path) ||
      this.router.url.includes(ApplicationUrls.GlobalToolSuite.CarrierManagement.path) ||
      this.router.url.endsWith(ApplicationUrls.GlobalToolSuite.ToolsManagement.path) ||
      this.router.url.includes(ApplicationUrls.GlobalToolSuite.ResourceCenter.path)) {
      return NavMenus.GlobalToolSuite;
    }

    if (this.router.url.includes(ApplicationUrls.GlobalToolSuite.UserManagement.path)) {
      return NavMenus.UserManagement;
    }

    const roleManagementPath = UrlUtils.replaceParamFromUrl(
      this.router.url,
      ApplicationUrls.GlobalToolSuite.RoleManagement.RoleToolManagement.path
    );
    if (this.router.url.includes(roleManagementPath)) {
      return NavMenus.RoleManagement;
    }

    if (this.router.url.includes(ApplicationUrls.GlobalToolSuite.ToolsManagement.path)) {
      return NavMenus.ToolManagement;
    }

    if (this.router.url == ApplicationUrls.Carrier411Admin.path ||
      this.router.url == ApplicationUrls.Carrier411Admin.LandingPageContentManagement.path ||
      this.router.url == ApplicationUrls.Carrier411Admin.MassUploadTool.path) {
      return NavMenus.Carrier411Admin;
    }

    return NavMenus.None;
  }

  replaceLinksParams(): void {
    this.navbar?.links?.filter(l => l.path.includes('{')).forEach(link => {
      link.path = this.replaceParamFromUrl(this.router.url, link.path)
    })
  }
}
