import {
  AfterViewChecked,
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  Renderer2,
  ViewChild
} from '@angular/core';

@Component({
  selector: 'expandable-content',
  templateUrl: './expandable-content.component.html',
  styleUrls: ['./expandable-content.component.scss']
})
export class ExpandableContentComponent implements AfterViewInit, AfterViewChecked {
  @Input('height')
  public height: string = '';

  @ViewChild('contentContainer')
  public contentContainer!: ElementRef;

  show = false;
  showButton = false;

  constructor(
    private renderer: Renderer2,
    private cd: ChangeDetectorRef
  ) { }

  ngAfterViewInit(): void {
    if (this.contentContainer && this.height != '') {
      this.setDefaultHeight();
      this.showButton = this.showViewButton();
    }
  }

  /* Problem: Expression has changed after it was checked */
  ngAfterViewChecked() {
    this.cd.detectChanges();
  }

  toggleContent(): void {
    this.show = !this.show;
    if (this.show) {
      this.setChildrenHeight();
    } else {
      this.setDefaultHeight();
    }
  }

  private setChildrenHeight() {
    this.renderer.setStyle(this.contentContainer.nativeElement, 'max-height', `${this.getChildrenHeight()}px`);
  }

  private setDefaultHeight() {
    this.renderer.setStyle(this.contentContainer.nativeElement, 'max-height', `${this.height}px`);
  }

  private showViewButton(): boolean {
    let containerHeight = this.contentContainer.nativeElement.offsetHeight;
    return this.getChildrenHeight() > containerHeight;
  }

  private getChildrenHeight(): number {
    let childrenHeight = 0;
    const elements = this.contentContainer.nativeElement.querySelectorAll(':scope > *');

    elements.forEach((e: any) => {
      childrenHeight += e.offsetHeight;
    });

    return childrenHeight;
  }
}
