import { Injectable } from '@angular/core';
import { take, tap } from 'rxjs/operators';
import { AuthTokenDTO } from '../interfaces/auth-token/auth-token.dto';
import { LoginDTO } from '../interfaces/login/login.dto';
import { WPUserAccountDTO } from '../interfaces/user/user-account.dto';
import { cryptoTools } from '../utilities/crypto-tools';
import { AccountApiService } from './account.api.service';
import { environment } from './../../../environments/environment';
import { ForgotPasswordDTO } from '../interfaces/account/forgot-password.dto';
import { ResetPasswordDTO } from '../interfaces/account/reset-password.dto';
import { UserType } from '../../shared/constants/user-type.enum';
import { GaitService } from './gait.service';
import { TokenDTO } from '../interfaces/auth-token/token.dto';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { Observable } from 'rxjs';

const USER_KEY = 'auth-user';

@Injectable()
export class AccountService {
  constructor(
    private accountApiService: AccountApiService,
    private gaitService: GaitService
  ) {
  }

  login(loginDto: LoginDTO) {
    return this.accountApiService.login(loginDto).pipe(
      take(1),
      tap((response) => {
        if (response.isSuccess) {
          let userAccountDTO: WPUserAccountDTO = response.value;
          this.setCurrentUser(userAccountDTO);
        }
      }),
    );
  }

  logout() {
    const user = this.getUser();
    //for brokers log out from gait
    if (user?.userType == UserType.Broker && user?.contextId) {
      this.gaitService.deleteContextId(user.contextId);
    }

    window.localStorage.removeItem(USER_KEY);
    //redirects to marketing site, signs out the user and navigates to homepage
    window.location.href = environment.marketing.baseUrl + '/sign-out';
  }

  public getCurrentUser(bypassLogoutOnNull = false) {
    const user = this.getUser();
    if ((!user || !this.isValidWPUserAccount(user)) && !bypassLogoutOnNull) {
      return null;
    }

    return Object.assign({}, (user || {} as WPUserAccountDTO))
  }

  public getCurrentUserId() {
    const currentUser = this.getUser();

    return (currentUser || {}).id;
  }

  public getCurrentUserAuthToken() {
    const currentUser = this.getUser();

    return (currentUser || {}).token;
  }

  public getCurrentWPUserAccountId() {
    const currentUser = this.getUser();

    return (currentUser || {}).accountId;
  }

  public isValidAuthPersisted(): boolean {
    const userAccount = this.getUser();
    if (!userAccount) return false;

    return this.isValidWPUserAccount(userAccount);
  }

  public forgotPassword(forgotPasswordDto: ForgotPasswordDTO) {
    return this.accountApiService.forgotPassword(forgotPasswordDto);
  }

  public resetPassword(resetPasswordDto: ResetPasswordDTO) {
    return this.accountApiService.resetPassword(resetPasswordDto);
  }

  refreshToken(token: TokenDTO): Observable<ServiceResultDTO<TokenDTO>> {
    return this.accountApiService.refreshToken(token);
  }

  public getUser(): WPUserAccountDTO | null {
    const user = window.localStorage.getItem(USER_KEY);
    if (user) {
      return cryptoTools.decrypt(user) as WPUserAccountDTO;
    }

    return null;
  }

  public saveUser(user: WPUserAccountDTO): void {
    window.localStorage.removeItem(USER_KEY);

    const encrypted: string = cryptoTools.encryptObject(user);
    window.localStorage.setItem(USER_KEY, encrypted);
  }

  public updateTokens(token: TokenDTO) {
    const user = this.getUser();
    this.setCurrentUser({
      ...user!,
      token: token.accessToken,
      refreshToken: token.refreshToken
    });
  }

  public userHasRole(roleName: string): boolean {
    const user = this.getCurrentUser();
    return user != null && user.roles?.includes(roleName);
  }

  private isValidWPUserAccount(user: WPUserAccountDTO): boolean {
    if (typeof user == "undefined" || !user || !user.token) {
      return false;
    }

    const token: AuthTokenDTO = cryptoTools.decodeJwt(user.token);
    if (!token.nameid) {
      return false;
    }

    return true;
  }

  private setCurrentUser(user: WPUserAccountDTO) {
    user.roles = [];
    const roles = cryptoTools.decodeJwt(user.token).role;
    //if user is in one role JWT returns as string, if in multiple roles returns as array
    Array.isArray(roles) ? user.roles = roles : user.roles.push(roles);

    this.saveUser(user);
  }
}
