import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { SharedApiService } from '../services/shared.api.service';
import { ServiceResultDTO } from '../../core/interfaces/service-result/service-result.dto';
import { ToolDTO } from '../interfaces/tools/tool.dto';

@Injectable()
export class ToolListRolesResolver implements Resolve<ServiceResultDTO<ToolDTO[]>> {

  constructor(private toolService: SharedApiService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<ToolDTO[]>> {
    return this.toolService.getTools(true, false, undefined, true);
  }
}
