import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpErrorResponse } from '@angular/common/http';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { catchError, filter, switchMap, take } from 'rxjs/operators';
import { AccountService } from '../services/account.service';
import { AppStartupService } from 'src/app/core/services/app.startup.service';
import { TokenDTO } from '../interfaces/auth-token/token.dto';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';

const TOKEN_HEADER_KEY = 'Authorization';

@Injectable()
export class JwtInterceptor implements HttpInterceptor {
  private isRefreshing = false;
  private refreshTokenSubject: BehaviorSubject<any> = new BehaviorSubject<any>(null);

  constructor(
    private accountService: AccountService,
    private appStartupService: AppStartupService
  ) { }

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<Object>> {
    const user = this.accountService.getCurrentUser();
    if (user) {
      request = this.addTokenHeader(request, user.token);
    }

    return next.handle(request).pipe(catchError(error => {
      if (error instanceof HttpErrorResponse &&
        error.status === 401 &&
        !this.appStartupService.isOnAccountPage()) {
        return this.handle401Error(request, next);
      }

      return throwError(error);
    }));
  }

  private handle401Error(request: HttpRequest<any>, next: HttpHandler) {
    if (!this.isRefreshing) {
      this.isRefreshing = true;
      this.refreshTokenSubject.next(null);

      const user = this.accountService.getCurrentUser();
      if (!user) return next.handle(request);

      const tokenDTO: TokenDTO = {
        accessToken: user.token,
        refreshToken: user.refreshToken
      };

      return this.accountService.refreshToken(tokenDTO).pipe(
        switchMap((result: ServiceResultDTO<TokenDTO>) => {
          if (result.isSuccess) {
            const token = result.value;
            this.isRefreshing = false;

            this.accountService.updateTokens(token);
            this.refreshTokenSubject.next(token.accessToken);

            return next.handle(this.addTokenHeader(request, token.accessToken));
          } else {
            this.isRefreshing = false;

            this.accountService.logout();

            return next.handle(request);
          }
        }),
        catchError((err) => {
          this.isRefreshing = false;

          this.accountService.logout();
          return throwError(err);
        })
      );
    }

    return this.refreshTokenSubject.pipe(
      filter(token => token !== null),
      take(1),
      switchMap((token) => next.handle(this.addTokenHeader(request, token)))
    );
  }

  private addTokenHeader(request: HttpRequest<any>, token: string) {
    return request.clone({ headers: request.headers.set(TOKEN_HEADER_KEY, 'Bearer ' + token) });
  }
}
